<template>
  <div class="roomlist_wraper">
    <!-- 登录信息 -->
    <el-col :span="24" class="header">
      <el-col :span="10" class="logo logo-collapse-width">
        <div class="lesson-bg"></div>
        <img src="../../assets/img/newlogo.png" alt />
      </el-col>
      <el-col :span="10"></el-col>
      <el-col :span="4" class="userinfo">
        <img :src="this.sysUserAvatar" />
        <span>{{ sysUserName }}</span>
        <el-button type="text" style="color:white" icon="fa fa-sign-out " @click="logout"></el-button>
      </el-col>
    </el-col>

    <div class="container">
      <div v-for="(item,index) in data_list" class="room_item" :key="index">
        <div class="item_container" @click="clickItem(item)">
          <img src="../../assets/img/img_liveroom.png" alt />
          <div class="item_title">{{getname(item)}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  removeSession,
  removeUserid,
  setAnchorid
} from "@/utils/networks/auth";
export default {
  data() {
    return {
      userid: "",
      sysUserName: "",
      sysUserAvatar: "",
      data_list: []
    };
  },
  watch: {},
  computed: {},
  methods: {
    getname(item) {
      var name = "";
      if (item.docname && item.docname != "") {
        name = item.docname;
      }
      if (item.roomname && item.roomname != "") {
        name = item.roomname;
      }
      if (name == "") {
        name = item.mobile;
      }
      if (item.anchorid == this.userid) {
        name = name + "(当前用户)";
      }
      return name;
    },
    clickItem(item) {
      // _.dee
      this.$root.curLiveroom = _.cloneDeep(item);
      sessionStorage.setItem("room", JSON.stringify(_.cloneDeep(item)));
      setAnchorid(item.anchorid);
      this.$router.push({ path: "/courselist" });
    },
    // 退出登录
    logout: function() {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {
        // type: 'warning'
      })
        .then(() => {
          _this
            .getData({
              funcid: "1102"
            })
            .then(res => {
              if (res.code == 1) {
                sessionStorage.removeItem("user");
                removeSession();
                removeUserid();
                _this.$router.push("/login");
              }
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(() => {});
    }
  },
  created() {},
  activated() {},
  computed: {},
  mounted() {
    this.$nextTick(() => {
      var user = sessionStorage.getItem("user");

      if (user) {
        user = JSON.parse(user);
        this.userid = user.userid || "";
        this.sysUserName = user.docname || "";
        this.sysUserAvatar = user.picture || "";
        this.data_list = this.ArrayUnique(user.liverooms.reverse(), "anchorid");
      }
    });
  }
};
</script>
>

<style lang="scss" scoped>
.header {
  position: absolute;
  top: 0;
  height: 40px;
  line-height: 40px;
  background-color: #2f353b;
  color: #fff;
  .userinfo {
    position: absolute;
    right: 30px;
    height: 40px;
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    img {
      width: 30px;
      height: 30px;
      border-radius: 30px;
    }
    span {
      margin-left: 8px;
      margin-right: 8px;
    }
  }
  .logo {
    height: 40px;
    font-size: 22px;
    padding-left: 20px;
    padding-right: 20px;
    img {
      height: 24px;
      float: left;
      margin: 8px 0px 8px 0px;
    }
    .txt {
      color: #fff;
    }
  }
  .logo-width {
    width: 230px;
  }
  .logo-collapse-width {
    width: 60px;
  }
  .tools {
    padding: 0px 23px;
    width: 14px;
    height: 60px;
    line-height: 60px;
    cursor: pointer;
  }
}

.roomlist_wraper {
  height: 100%;
  width: 100%;
  background: #f0f0f0;
}
.container {
  background: #f0f0f0;
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.room_item {
  padding: 5px;
  .item_container {
    cursor: pointer;
    width: 200px;
    height: 260px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    img {
      width: 120px;
      height: 120px;
    }
    .item_title {
      height: 50px;
      line-height: 50px;
    }
  }
}
</style>


