<template>
  <section v-if="hasAuthority">
    <section v-if="$route.path != rootPath">
      <router-view></router-view>
    </section>
    <section v-else>
      <el-row class="topArea">
        <el-col :span="8">
          <div>
            <div>{{totalfee}}</div>
            <div>总收益</div>
          </div>
        </el-col>
        <el-col :span="8">
          <div>
            <div>{{fans}}</div>
            <div>粉丝数</div>
          </div>
        </el-col>
        <el-col :span="8">
          <div>
            <div>{{fee}}</div>
            <div>当前金额</div>
          </div>
        </el-col>
      </el-row>

      <!--列表-->
      <el-table
        :data="data_list"
        stripe
        border
        highlight-current-row
        v-loading="listLoading"
        style="width: 100%;"
      >
        <el-table-column prop="title" label="课程名称" width="280"></el-table-column>
        <el-table-column prop="classtype" label="课程类型" width="120"></el-table-column>
        <el-table-column prop="clicknum" label="访客数" width="120"></el-table-column>
        <el-table-column prop="buynum" label="报名数" width="120"></el-table-column>
        <el-table-column prop="earnings" label="主播佣金"></el-table-column>

        <el-table-column label="操作" width="140">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-pagination
          background
          layout="sizes,prev,pager,next,slot"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="curPage"
          :page-sizes="[10, 20, 30, 50]"
          :page-size="pageSize"
          :total="total"
          style="float:right;"
        >
          <span>&nbsp;共{{Math.ceil(total/pageSize)}}页</span>
        </el-pagination>
      </el-col>
    </section>
  </section>
  <section v-else>
    <no-authority></no-authority>
  </section>
</template>

<script>
// import NProgress from 'nprogress'
import {
  getUserListPage,
  removeUser,
  editUser
} from "../../utils/networks/api";
import NoAuthority from "../common/NoAuthority";
export default {
  components: {
    NoAuthority
  },
  data() {
    return {
      rootPath: "/statisticslist",
      hasAuthority: true,
      room: "",
      user: "",
      totalfee: 1000,
      fans: 120,
      fee: 100,
      pageSize: 10,
      curPage: 1,
      total: 1,
      data_list: [],
      listLoading: false
    };
  },
  methods: {
    refrehList() {
      var _this = this;
      _this.listLoading = true;
      _this
        .getData({
          funcid: "1018",
          anchorid: this.room.anchorid,
          page: this.curPage,
          pagesize: this.pageSize
        })
        .then(res => {
          if (res.code == 1) {
            _this.curPage = res.data.page;
            _this.pageSize = res.data.pagesize;
            _this.data_list = res.data.records;
            _this.totalfee = res.data.incomefee;
            _this.fans = res.data.fans;
            _this.fee = res.data.surplusfee;
          }
          _this.listLoading = false;
        });
    },
    handleSizeChange(value) {
      this.pageSize = value;
    },
    handleCurrentChange(value) {
      this.curPage = value;
    },
    handleEdit(index, row) {
      console.log(index);
      this.$router.push({
        path: "/statisticslist/staticsDetail",
        query: { classid: row.classid, classtype: row.classtype }
      });
    }
  },
  watch: {
    dialogFormVisible(val) {
      if (val) {
        // this.resetsearchData()
      }
    }
  },
  mounted() {
    var _this = this;
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = "";
      var room = sessionStorage.getItem("room");
      var user = sessionStorage.getItem("user");
      if (user) {
        user = JSON.parse(user);
      }
      if (room) {
        room = JSON.parse(room);
        _this.room = room;
        _this.refrehList();
      }
      _this.hasAuthority = user.userid == room.anchorid;
    });
  }
};
</script>

<style scoped>
.topArea {
  padding-top: 5px;
  padding-bottom: 10px;
}
.toolbar {
  padding: 10px;
}
</style>


