<template>
  <div class="single_wraper">
    <el-form :rules="rules" ref="courseinfo" :model="course" label-width="90px">
      <!-- <el-form-item label="课程分类">
        <el-select class="input_common"
                   v-model="course.classtype"
                   placeholder="请选择活动区域">
          <el-option label="区域一"
                     value="shanghai"></el-option>
          <el-option label="区域二"
                     value="beijing"></el-option>
        </el-select>
      </el-form-item>-->
      <el-form-item label="课程名称" prop="title">
        <el-input maxlength="20" class="input_common" v-model="course.title"></el-input>
      </el-form-item>

      <!-- 讲师头像 -->
      <el-form-item label="系列课展示封面" prop="banner1" style="width:60%;min-width:600px;">
        <span>
          <img :src="course.banner1" class="head_icon" :onerror="errorImg">
        </span>
        <el-upload
          class="upload-demo"
          accept="image/png, image/jpg"
          action
          :auto-upload="false"
          :show-file-list="false"
          :limit="1"
          :file-list="fileList"
          :on-change="changeUpload1"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">支持上传jpg/png文件，且不超过5M</div>
        </el-upload>
      </el-form-item>
      <!-- 课程封面 -->
      <el-form-item label="课程封面" prop="banner" style="width:60%;min-width:600px;">
        <span>
          <img :src="course.banner" class="head_icon" :onerror="errorImg">
        </span>
        <el-upload
          class="upload-demo"
          accept="image/png, image/jpg"
          action
          :auto-upload="false"
          :show-file-list="false"
          :limit="1"
          :file-list="fileList"
          :on-change="changeUpload"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">支持上传jpg/png文件，且不超过5M</div>
        </el-upload>
      </el-form-item>

      <el-form-item label="系列课简介" prop="comment">
        <el-input
          class="input_common"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 8}"
          placeholder="请输入内容"
          v-model="course.comment"
        ></el-input>
        <span class>111</span>
      </el-form-item>

      <el-form-item label="简介图片" prop="detail">
        <el-col>
          <photo-drag-wraper
            :uploadTitle="uploadDetailTitle"
            :list="course.detail"
            rowHeight="80"
            column="6"
          ></photo-drag-wraper>
        </el-col>
      </el-form-item>

      <div v-if="course.seriesid">
        <el-form-item label="课程总价">
          <span class="fl fc_333333 mal-10">{{course.totalfee?course.totalfee:0}}元</span>
          <span class="fl fc_333333 mal-10"></span>
          <span class="fl fc_888888">&nbsp;(0为免费)</span>
        </el-form-item>

        <el-form-item label="课程费用">
          <el-input
            type="Number"
            class="w-100 float-l"
            v-on:input="changeFee2"
            v-model="course.fee"
          ></el-input>
          <span class="fl fc_333333 mal-10">元 &nbsp;&nbsp;折扣&nbsp;</span>
          <el-input
            type="Number"
            class="w-90 float-l"
            v-on:input="changeRate2"
            v-model="course.discount"
          ></el-input>
          <span class="fl fc_333333 mal-10">%</span>
          <span class="fl fc_888888">&nbsp;(支持0-100整数)</span>
        </el-form-item>
      </div>
      <div v-else>
        <el-form-item label="折扣" prop="discount">
          <el-input
            type="Number"
            class="w-80 float-l"
            v-on:input="changeRate"
            v-model="course.discount"
          ></el-input>
          <span class="fl fc_333333 mal-10">%</span>
          <span class="fl fc_888888">&nbsp;(支持0-100整数)</span>
        </el-form-item>
      </div>

      <el-form-item>
        <el-button type="primary" @click="onSubmit" :loading="saveLoading">保存</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </el-form-item>
    </el-form>

    <!-- vueCropper 剪裁图片实现-->
    <el-dialog title="图片剪裁" :visible.sync="dialogVisible" append-to-body>
      <div class="cropper-content">
        <div class="cropper" style="text-align:center">
          <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="true"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
          ></vueCropper>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="finish(option.fixedNumber)" :loading="loading">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import photoDragWraper from '@/components/photoDragWraper'
import VideoUpload from '@/components/VideoUpload'
import Host from '@/utils/networks/api'
export default {
  components: {
    photoDragWraper,
    VideoUpload
  },
  data() {
    return {
      uploadDetailTitle: '上传简介图片',
      isEdited: false,
      saveLoading: false,
      errorImg: 'this.src="' + require('@/assets/img/default_nodata.png') + '"',
      baseurl: location.host,
      dialogVisible: false,
      loading: false,
      // 裁剪组件的基础配置option
      option: {
        img: '', // 裁剪图片的地址
        info: true, // 裁剪框的大小信息
        outputSize: 0.8, // 裁剪生成图片的质量
        outputType: 'jpeg', // 裁剪生成图片的格式
        canScale: false, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        // autoCropWidth: 300, // 默认生成截图框宽度
        // autoCropHeight: 200, // 默认生成截图框高度
        fixedBox: false, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [16, 9], // 截图框的宽高比例
        full: true, // 是否输出原图比例的截图
        canMoveBox: true, // 截图框能否拖动
        original: false, // 上传图片按照原始比例渲染
        centerBox: true, // 截图框是否被限制在图片里面
        infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      },
      errorImg: 'this.src="' + require('@/assets/img/default_nodata.png') + '"',
      fileList: [],
      course: {
        seriesid: '',
        classtype: '',
        title: '',
        banner: '',
        banner1: '',
        detail: [],
        comment: '',
        fee: '0',
        discount: '',
        ppt: [
          {
            url: '',
            name: ''
          }
        ]
      },
      rules: {
        title: [
          { required: true, message: '请输入课程标题', trigger: 'change' }
        ],
        banner: [
          {
            required: true,
            message: '请选择课程封面',
            trigger: 'blur'
          }
        ],
        comment: [
          { required: true, message: '请填写课程简介', trigger: 'change' }
        ],
        discount: [
          { required: true, message: '请输入课程折扣', trigger: 'change' }
        ]
      }
    }
  },
  watch: {
    // 如果 `question` 发生改变，这个函数就会运行
    question(newQuestion, oldQuestion) {
      // this.answer = 'Waiting for you to stop typing...'
    },
    course: {
      handler(newValue, oldValue) {
        this.isEdited = true
      },
      deep: true
    }
  },
  computed: {
    // 计算属性的 getter
    reversedMessage() {
      return ''
    }
  },
  methods: {
    changeFee2(value) {
      var _this = this
      _this.$nextTick(() => {
        if (!value || value < 0) {
          _this.course.fee = 0
        }
        if (parseFloat(value) > 100) {
          _this.course.fee = 100
        }
        _this.course.discount = (_this.course.fee / _this.course.totalfee) * 100
      })
    },
    changeRate2(value) {
      var _this = this
      _this.$nextTick(() => {
        if (!value || value < 0) {
          _this.course.discount = 0
        }
        if (parseFloat(value) > 100) {
          _this.course.discount = 100
        }
        _this.course.fee = _this.course.totalfee * (_this.course.discount / 100)
      })
    },
    changeRate(value) {
      var _this = this
      if (!value || value < 0) {
        _this.$nextTick(() => {
          _this.course.discount = 0
        })
      }
      if (parseFloat(value) > 100) {
        _this.$nextTick(() => {
          _this.course.discount = 100
        })
      }
    },
    requestData() {
      var _this = this
      _this
        .getData({
          funcid: '1007',
          seriesid: _this.course.seriesid
        })
        .then(res => {
          if (res.code == 1) {
            _this.course = res.data.records[0]
            if (!_this.course.detail) {
              _this.course.detail = []
            }
            setTimeout(() => {
              _this.isEdited = false
            }, 0)
          }
        })
    },
    cancelAction() {
      var _this = this
      if (_this.course.seriesid && _this.isEdited) {
        _this
          .$confirm('是否保存修改?', '提示', {
            type: 'warning'
          })
          .then(() => {
            _this.onSubmit()
          })
          .catch(() => {
            _this.goback()
          })
      } else {
        _this.goback()
      }
    },
    goback() {
      this.$router.back(-1)
    },
    onSubmit() {
      var _this = this
      _this.$refs.courseinfo.validate(valid => {
        console.log(valid)
        _this.saveloading = true
        if (valid) {
          var param = this.course
          param.funcid = this.course.seriesid ? '1008' : '1011'
          _this.saveLoading = true
          _this.getData(param).then(res => {
            if (res.code == 1) {
              _this.goback()
            }
            _this.saveLoading = false
          })
        } else {
          _this.saveLoading = false
        }
      })
    },
    // 上传按钮   限制图片大小
    changeUpload(file, fileList) {
      this.option.fixedNumber = [16, 9]
      var _this = this
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isLt5M) {
        this.$message.error('上传文件大小不能超过 5MB!')
        this.fileList = []
        return false
      }
      var filetype = file.raw.type.toLowerCase()
      if (
        filetype.indexOf('jpg') == -1 &&
        filetype.indexOf('jpeg') == -1 &&
        filetype.indexOf('png') == -1
      ) {
        this.$message.error('图片类型错误')
        this.fileList = []
        return false
      }

      this.fileinfo = file
      const imgurl = URL.createObjectURL(file.raw)
      console.log(imgurl)
      // 上传成功后将图片地址赋值给裁剪框显示图片
      _this.$nextTick(() => {
        _this.option.img = imgurl
        _this.dialogVisible = true
      })
      _this.fileList = []
    },
    changeUpload1(file, fileList) {
      this.option.fixedNumber = [1, 1]
      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isLt5M) {
        this.$message.error('上传文件大小不能超过 5MB!')
        this.fileList = []
        return false
      }
      var filetype = file.raw.type.toLowerCase()
      if (
        filetype.indexOf('jpg') == -1 &&
        filetype.indexOf('jpeg') == -1 &&
        filetype.indexOf('png') == -1
      ) {
        this.$message.error('图片类型错误')
        this.fileList = []
        return false
      }

      this.fileinfo = file
      const imgurl = URL.createObjectURL(file.raw)
      // 上传成功后将图片地址赋值给裁剪框显示图片
      this.$nextTick(() => {
        this.option.img = imgurl
        this.dialogVisible = true
      })
      this.fileList = []
    },
    // 点击裁剪，这一步是可以拿到处理后的地址
    finish(ratio) {
      var _this = this
      this.$refs.cropper.getCropBlob(data => {
        var fileName = 'goods' + _this.fileinfo.uid
        this.loading = true
        // 上传阿里云服务器
        setTimeout(() => {
          // var file = _this.convertBase64UrlToBlob(data)
          const imgurl = URL.createObjectURL(data)
          const param = new FormData() // 创建form对象
          param.append('file', data, _this.fileinfo.name) // 通过append向form对象添加数据
          param.append('filetype', 'jpeg')
          if (_this.IsArrayEqual(ratio, [16, 9])) {
            _this.uploadImage(param).then(res => {
              if (res.code == 1) {
                console.log(res.list.url)
                _this.course.banner = res.list.url
              }
              _this.loading = false
              _this.dialogVisible = false
              _this.$refs.courseinfo.validateField('banner')
            })
          } else if (_this.IsArrayEqual(ratio, [1, 1])) {
            _this.uploadImage(param).then(res => {
              if (res.code == 1) {
                console.log(res.list.url)
                _this.course.banner1 = res.list.url
              }
              _this.loading = false
              _this.dialogVisible = false
              _this.$refs.courseinfo.validateField('banner1')
            })
          }
        }, 3000)
      })
    }
  },
  created() {},
  activated() {},
  mounted() {
    var _this = this
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = ''
    })
    _this.course.seriesid = _this.$route.query.seriesid
    if (_this.course.seriesid) {
      _this.requestData()
    }
  }
}
</script>


<style lang="scss" scoped>
.single_wraper {
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
  text-align: left;
}

.head_icon {
  // float: left;
  display: inline-block;
  width: auto;
  height: 100px;
}
.upload-demo {
  display: inline-block;
  padding-top: 40px;
  margin-left: 20px;
}
.content {
  text-align: left;
}
.el-upload__tip {
  margin-left: 8px;
  display: inline-block;
}

.input_common {
  width: 280px;
  float: left;
}
.fl {
  float: left;
}
.fc_888888 {
  color: #888888;
}
.fc_333333 {
  color: #333333;
}
/deep/ .el-input__inner {
  height: 30px;
  line-height: 30px;
  border-radius: 2px;
}
/deep/ .el-input__icon {
  line-height: 30px;
}
/deep/ .el-textarea__inner {
  border-radius: 2px;
}

// 截图
.cropper-content {
  .cropper {
    width: auto;
    height: 300px;
  }
}
</style>


