<template>
  <div class="mywraper">
    <!--列表-->
    <el-table
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="name" label="模块名称" width="120"></el-table-column>
      <el-table-column prop="displayorder" label="排序" width="90"></el-table-column>
      <el-table-column prop="datastatus" label="状态">
        <template slot-scope="scope">
          <span
            :class="[scope.row.datastatus == 2 ? 'fc-ff3a3a':'']"
          >{{formatDatastatus(scope.row,scope.column)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="handleEdit(scope.$index, scope.row,'status')"
          >{{scope.row.datastatus == 1?'停用':'启用'}}</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row,'up')">上移</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row,'down')">下移</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data_list: [],
      listLoading: false
    };
  },
  watch: {},
  computed: {},
  methods: {
    refreshData() {
      var _this = this;
      const para = {
        funcid: "1106"
      };
      _this.listLoading = true;
      _this.getData(para).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          _this.data_list = res.data.records;
        }
        _this.listLoading = false;
      });
    },
    formatDatastatus: function(row, column) {
      if (row.datastatus == 0) {
        return "删除";
      } else if (row.datastatus == 1) {
        return "正常";
      } else if (row.datastatus == 2) {
        return "停用";
      }
      return "未知";
    },
    handleEdit: function(index, row, operation) {
      var _this = this;
      var alertData = Object.assign({}, row);
      if (operation == "status") {
        if (row.datastatus == 1) {
          alertData.datastatus = "2";
          _this.edtModule(alertData);
        } else if (row.datastatus == 2) {
          alertData.datastatus = "1";
          _this.edtModule(alertData);
        }
      } else if (operation == "up" || operation == "down") {
        alertData.operation = operation;
        _this.edtModule(alertData);
      }
    },
    edtModule(alterData) {
      var _this = this;
      var param = alterData;
      param.funcid = 1107;
      _this.listLoading = true;
      _this.getData(param).then(res => {
        if (res.code == 1) {
          _this.refreshData();
        }
        _this.listLoading = false;
      });
    }
  },
  created() {},
  activated() {
    var _this = this;
    _this.refreshData();
  },
  mounted() {
    var _this = this;
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = "";
    });
    _this.refreshData();
  }
};
</script>
>

<style lang="scss" scoped>
.mywraper {
  color: white;
}
</style>


