<template id = "dragView">
  <div id="photo_wraper">
    <div class="pic_container">
      <el-row v-for="(rowList, i) in localList" class="row" :key="i" :gutter="20">
        <el-col :span="24/maxColumn" v-for="(item,index) in rowList" :key="index">
          <div
            id="img_container"
            :class="['square' ,activeIndex == i*maxColumn + index? 'sel_border':'nor_border']"
            :style="{height:imgHeight?imgHeight+'px':'100%'}"
            @click="selItem(i*maxColumn + index)"
            @dragstart="dragstart(i*maxColumn + index,$event)"
            @dragend="dragend($event)"
            @drop="drop(i*maxColumn + index,$event)"
            @dragover="dragover($event)"
            draggable="true"
          >
            <!--  -->
            <img class="img" :src="item.url" :height="imgHeight?imgHeight+'px':'100%'" alt>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- :action="uploadUrl()" :auto-upload="false" :data="extraData"-->
    <el-upload
      id="pic_upload"
      ref="upload"
      accept="image/png, image/jpg"
      action
      multiple
      :show-file-list="false"
      :on-change="onchange"
      :on-success="onsuccess"
      :before-upload="beforeUpload"
      :file-list="list"
    >
      <el-button size="small" type="primary" :loading="uploading">{{uploadTitle}}</el-button>
      <el-button size="small" @click.stop="moveleft">左移</el-button>
      <el-button size="small" @click.stop="moveright">右移</el-button>
      <el-button size="small" @click.stop="del">删除</el-button>
      <span class="fc-888888">&nbsp;(注:可拖拽图片交换位置)</span>
      <div slot="tip" class="el-upload__tip tex-l">支持上传jpg/png文件，且不超过5M</div>
    </el-upload>
  </div>
</template>

<script>
import Vue from "vue";
import { baseUrl } from "@/utils/networks/api";
export default {
  props: {
    // 行高
    rowHeight: [String, Number],
    // 每行的个数
    column: {
      default: 3
    },
    uploadTitle: {
      type: String,
      default: "请上传PPT"
    },
    list: {
      type: Array,
      default: []
    }
  },
  data: () => ({
    extraData: {},
    uploading: false,
    // list: [],
    maxColumn: 3,
    moveIndex: null,
    targetIndex: null,
    imgHeight: null,
    activeIndex: 0
  }),
  computed: {
    localList() {
      var maxColumn = this.maxColumn; // 列数
      var totalCount = this.list ? this.list.length : 0; // 总数量
      var rows = Math.ceil(totalCount / maxColumn); // 行数
      var localList = [];
      for (var i = 0; i < totalCount; i++) {
        var item = this.list[i];
        var currentRow = Math.floor(i / maxColumn);
        var arr = localList[currentRow];
        if (arr == "undefined" || arr == null) {
          arr = [];
          localList.push(arr);
        }
        arr.push(item);
      }
      return localList;
    }
  },
  watch: {
    imagelist(val) {
      this.list = val;
    }
  },
  methods: {
    uploadUrl: function() {
      return baseUrl + "/course/uploadimage";
    },
    // 交换元素
    swapArray(arr, index1, index2) {
      if (index1 < 0 || index1 > arr.length - 1) {
        return arr;
      }
      if (index2 < 0 || index2 > arr.length - 1) {
        return arr;
      }
      var temp = arr[index1];
      Vue.set(arr, index1, arr[index2]);
      Vue.set(arr, index2, temp);
      this.activeIndex = index2;
      return arr;
    },
    selItem(index) {
      this.activeIndex = index;
    },
    moveleft() {
      var index = this.activeIndex;
      var moveIndex = index - 1;
      this.swapArray(this.list, index, moveIndex);
    },
    moveright() {
      var index = this.activeIndex;
      var moveIndex = index + 1;
      this.swapArray(this.list, index, moveIndex);
    },
    del() {
      var _this = this;
      if (_this.list.length == 0) {
        return;
      }
      _this
        .$confirm("确认删除该图片吗?", "提示", {
          type: "warning"
        })
        .then(() => {
          var index = _this.activeIndex;
          if (index > -1) {
            _this.list.splice(index, 1);
            _this.activeIndex = 0;
          }
        })
        .catch(() => {});
    },
    getImgUrl(raw) {
      return window.URL.createObjectURL(raw);
    },
    dragstart(index) {
      var _this = this;
      console.log("拖放开始", index);
      _this.moveIndex = index;
      _this.activeIndex = index;
    },
    dragover(event) {
      // 必须添加dragover事件，不然drop事件也无效
      event.preventDefault();
    },
    dragend(event) {
      console.log("拖放结束", event.currentTarget);
    },
    drop(index, event) {
      var _this = this;
      // 阻止默认行为
      event.preventDefault();
      // 阻止默认行为
      event.stopPropagation();
      console.log("拖放到目标", index);
      var moveIndex = _this.moveIndex;
      var targetIndex = index;
      var arr = _this.list;
      _this.targetIndex = index;
      // 交换位置
      var temp = arr[targetIndex];
      Vue.set(arr, targetIndex, arr[moveIndex]);
      Vue.set(arr, moveIndex, temp);
      _this.activeIndex = targetIndex;
    },
    allowDrop(event) {
      event.preventDefault();
    },
    onchange(file, fileList) {},
    beforeUpload(file) {
      var _this = this;
      var filetype = file.type.toLowerCase();
      var filename = file.name;
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        this.$message.error(filename + "的上传文件大小超过5M!");
        _.pull(this.list, file);
        return false;
      }
      if (
        filetype.indexOf("jpg") == -1 &&
        filetype.indexOf("jpeg") == -1 &&
        filetype.indexOf("png") == -1
      ) {
        this.$message.error(filename + "图片类型错误");
        _.pull(this.list, file);
        return false;
      }
      const param = new FormData(); // 创建form对象
      param.append("file", file, file.name); // 通过append向form对象添加数据
      param.append("filetype", file.type.split("/")[1]);
      _this.uploading = true;
      _this
        .uploadImage(param)
        .then(res => {
          if (res.code == 1) {
            _this.list.push({
              name: file.name,
              url: res.list.url
            });
          }
          _this.uploading = false;
        })
        .catch(error => {
          _this.uploading = false;
        });
      return false;
    },
    onsuccess(response, file, fileList) {}
  },
  created() {
    this.maxColumn = this.column;
    if (this.rowHeight) {
      this.imgHeight = this.rowHeight;
    }
  },
  mounted() {}
};
</script>


<style scoped>
.el-row {
  margin-bottom: 20px;
}
#photo_wraper {
  min-height: 280px;
  max-height: 320px;
  background-color: white;
}
.pic_container {
  min-height: 180px;
  max-height: 240px;
  overflow-y: scroll;
  overflow-x: hidden;
  border: 1px solid #f0f0f0;
}
#pic_upload {
  margin-top: 5px;
  float: left;
  bottom: 0;
}
.el-upload__tip {
  margin-top: 0;
}
.el-form-item {
  margin-bottom: 8px;
}
.square {
  background: #f0f0f0;
  padding: 0;
  height: 100%;
}
.nor_border {
  border: solid 2px white;
}
.sel_border {
  border: solid 2px #146487;
}
#img_container {
  display: flex;
  align-content: center;
  justify-content: center;
}
.img {
  width: auto;
  max-width: 100%;
  max-height: 100%;
}
</style>


