import { postData, getData, uploadImage } from './networks/api'
import md5 from 'js-md5'

// var OSS = require('ali-oss')
const pubfunc = {}
pubfunc.install = function(Vue, options) {
  Vue.prototype.isAndorid = function(rem) {
    var u = navigator.userAgent
    if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
      return true
    }
    return false
  }
  // 获取服务器时间
  Vue.prototype.getServerDate = function() {
    var xhr = null
    if (window.XMLHttpRequest) {
      xhr = new window.XMLHttpRequest()
    } else {
      // ie
      xhr = new ActiveObject('Microsoft')
    }

    xhr.open('GET', '/', false) // false不可变
    xhr.send(null)
    var date = xhr.getResponseHeader('Date')
    return new Date(date)
  }

  Vue.prototype.CalcuMD5 = function(pwd) {
    pwd = pwd.toUpperCase()
    pwd = md5(pwd)
    return pwd
  }

  Vue.prototype.getData = getData
  Vue.prototype.postData = postData
  Vue.prototype.uploadImage = uploadImage

  /**
   * 时间日期格式化
   * @param format
   * @returns {*}
   */
  Vue.prototype.FormatDate = function(dateObj, format) {
    const date = {
      'M+': dateObj.getMonth() + 1,
      'd+': dateObj.getDate(),
      'h+': dateObj.getHours(),
      'm+': dateObj.getMinutes(),
      's+': dateObj.getSeconds(),
      'q+': Math.floor((dateObj.getMonth() + 3) / 3),
      'S+': dateObj.getMilliseconds()
    }
    if (/(y+)/i.test(format)) {
      format = format.replace(
        RegExp.$1,
        (dateObj.getFullYear() + '').substr(4 - RegExp.$1.length)
      )
    }
    for (const k in date) {
      if (new RegExp('(' + k + ')').test(format)) {
        format = format.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? date[k]
            : ('00' + date[k]).substr(('' + date[k]).length)
        )
      }
    }
    return format
  }

  /**
   * 比较两个数组是否相等
   */
  Vue.prototype.IsArrayEqual = function(arr1, arr2) {
    if (arr1.length !== arr2.length) {
      return false
    }
    for (let i = 0; i < arr1.length; i++) {
      const item = arr1[i]
      const item1 = arr2[i]
      if (item !== item1) {
        return false
      }
    }
    return true
  }
  /**
   * 数组根据对象属性去重
   * @param arr 数组
   * @param name 去重的属性值
   * @returns [] 目标数组
   */
  Vue.prototype.ArrayUnique = function arrayUnique(arr, name) {
    var hash = {}
    return arr.reduce(function(item, next) {
      hash[next[name]] ? '' : (hash[next[name]] = true && item.push(next))
      return item
    }, [])
  }

  // 将base64的图片转换为file文件
  Vue.prototype.convertBase64UrlToBlob = function(urlData) {
    let bytes = ''
    if (urlData instanceof String) {
      bytes = window.atob(urlData.split(',')[1]) // 去掉url的头，并转换为byte
    } else if (urlData instanceof Object) {
      bytes = window.atob(urlData.size) // 去掉url的头，并转换为byte
    }

    // 处理异常,将ascii码小于0的转换为大于0
    const ab = new ArrayBuffer(bytes.length)
    const ia = new Uint8Array(ab)
    for (var i = 0; i < bytes.length; i++) {
      ia[i] = bytes.charCodeAt(i)
    }
    return new Blob([ab], {
      type: 'image/jpeg'
    })
  }
}

export default pubfunc



