<template>
  <el-row class="container">
    <!-- 登录信息 -->
    <el-col :span="24" class="header">
      <el-col :span="10" class="logo logo-collapse-width">
        <div class="lesson-bg"></div>
        <img src="../assets/img/logo2.png" alt />
      </el-col>
      <el-col :span="10"></el-col>

      <!-- class="userinfo" -->
      <el-col :span="4">
        <span class="userinfo" @visible-change="(val)=>{dropdownShow = val}">
          <span style="color:white;display: flex;align-items: center;justify-content: flex-end;">
            <img :src="this.sysUserAvatar" />
            <span class="fc-white">{{ sysUserName }}</span>
            <!-- <span :class="[dropdownShow?'el-icon-caret-top':'el-icon-caret-bottom','fc-white']"></span> -->
            <el-button
              type="text"
              style="color:white;margin-left:5px;"
              icon="fa fa-sign-out "
              @click="logout"
            ></el-button>
          </span>
          <!-- <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="changeRoom">切换直播间</el-dropdown-item>
            <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>-->
        </span>
      </el-col>
    </el-col>
    <!-- 下方区域 -->
    <template v-if="$route.path == '/roomlist'">
      <keep-alive>
        <!--会被缓存的视图组件-->
        <router-view v-if="$route.meta.keepAlive" class="router-view"></router-view>
      </keep-alive>
      <!--不会被缓存的视图组件-->
      <router-view v-if="!$route.meta.keepAlive" class="router-view"></router-view>
    </template>
    <template v-else>
      <el-col :span="24" class="main">
        <aside class="menu-expanded">
          <left-nav></left-nav>
        </aside>
        <section class="content-container">
          <div class="grid-content bg-purple-light">
            <el-col :span="24" class="breadcrumb-container">
              <bread-crumb></bread-crumb>
            </el-col>
            <el-col :span="24" class="content-wrapper">
              <!-- <transition name="fade"
              mode="out-in">-->
              <keep-alive>
                <!--会被缓存的视图组件-->
                <router-view v-if="$route.meta.keepAlive"></router-view>
              </keep-alive>
              <router-view v-if="!$route.meta.keepAlive"></router-view>
              <!-- </transition> -->
            </el-col>
          </div>
        </section>
      </el-col>
    </template>
  </el-row>
</template>

<script>
import leftNav from "@/components/leftNav";
import breadCrumb from "@/components/breadcrumb";
import { removeSession, removeUserid } from "@/utils/networks/auth";
export default {
  components: { leftNav, breadCrumb },
  data() {
    return {
      sysName: "App学习管理后台",
      collapsed: false,
      dropdownShow: false,
      sysUserName: "",
      sysUserAvatar: "",
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      }
    };
  },
  methods: {
    onSubmit() {
      console.log("submit!");
    },
    handleopen() {
      // console.log('handleopen');
    },
    handleclose() {
      // console.log('handleclose');
    },
    handleselect: function(a, b) {},
    changeRoom() {
      this.$router.replace({ path: "/roomlist" });
    },
    // 退出登录
    logout: function() {
      var _this = this;
      this.$confirm("确认退出吗?", "提示", {
        // type: 'warning'
      })
        .then(() => {
          _this
            .getData({
              funcid: "1002"
            })
            .then(res => {
              if (res.code == 1) {
                sessionStorage.removeItem("user");
                removeSession();
                removeUserid();
                _this.$router.push("/login");
              }
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(() => {});
    },
    // 折叠导航栏
    collapse: function() {
      this.collapsed = !this.collapsed;
    },
    showMenu(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
        "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    }
  },
  mounted() {
    var user = sessionStorage.getItem("user");
    if (user) {
      user = JSON.parse(user);
      this.sysUserName = user.docname || "";
      this.sysUserAvatar = user.picture || "";
    }
  }
};
</script>

<style scoped lang="scss">
// @import "~scss_vars";

.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  .header {
    height: 40px;
    line-height: 40px;
    background-color: #2f353b;
    color: #fff;
    .userinfo {
      position: absolute;
      right: 30px;
      height: 40px;
      text-align: right;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      img {
        margin: auto;
        width: 30px;
        height: 30px;
        border-radius: 30px;
      }
      span {
        margin-left: 4px;
        // margin-right: 4px;
      }
    }
    .logo {
      height: 40px;
      font-size: 22px;
      padding-left: 20px;
      padding-right: 20px;
      img {
        height: 18px;
        float: left;
        margin: 8px 0px 8px 0px;
      }
      .txt {
        color: #fff;
      }
    }
    .logo-width {
      width: 230px;
    }
    .logo-collapse-width {
      width: 60px;
    }
    .tools {
      padding: 0px 23px;
      width: 14px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
    }
  }
  .main {
    display: flex;
    // background: #324057;
    position: absolute;
    top: 40px;
    bottom: 0px;
    overflow: hidden;
    aside {
      flex: 0 0 230px;
      width: 230px;
      // position: absolute;
      // top: 0px;
      // bottom: 0px;
      .el-menu {
        width: 230px;
        height: 100%;
      }
      .collapsed {
        background-color: #252a2f;
        width: 60px;
        .item {
          position: relative;
        }
        .submenu {
          position: absolute;
          top: 0px;
          left: 60px;
          z-index: 99999;
          height: auto;
          display: none;
        }
      }
    }
    .menu-collapsed {
      // flex: 0 0 60px;
      width: 60px;
    }
    .menu-expanded {
      flex: 0 0 230px;
      width: 230px;
    }
    .content-container {
      // background: #f1f2f7;
      flex: 1;
      // position: absolute;
      // right: 0px;
      // top: 0px;
      // bottom: 0px;
      // left: 230px;
      overflow-y: scroll;
      padding: 20px;
      .breadcrumb-container {
        //margin-bottom: 15px;
        .title {
          width: 200px;
          float: left;
          color: #475669;
        }
        .breadcrumb-inner {
          float: left;
        }
      }
      .content-wrapper {
        background-color: #fff;
        box-sizing: border-box;
      }
    }
  }
}

.el-submenu__title:hover,
.el-submenu__title:focus {
  outline: none;
  background-color: #146487;
}

.el-form--inline .el-form-item__label {
  color: #146487;
  font-size: #146487;
}
</style>


