<template>
  <div class="wraper">
    <el-form
      :model="loginInfo"
      :rules="curIndex == 0 ? rules2 : rules1"
      ref="loginInfo"
      label-position="left"
      label-width="0px"
      class="demo-ruleForm login-container"
    >
      <img src="../../assets/img/logo.png" />
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="账号密码" name="first">
          <el-form-item prop="account">
            <el-input
              ref="account"
              maxlength="11"
              type="Number"
              v-model="loginInfo.account"
              auto-complete="off"
              placeholder="请输入账号"
              @keyup.enter.native="loginAction"
            >
              <i slot="prefix" class="el-input__icon icon_user"></i>
            </el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input
              ref="pwd"
              type="password"
              v-model="loginInfo.checkPass"
              auto-complete="off"
              placeholder="请输入密码"
              @keyup.enter.native="loginAction"
            >
              <i slot="prefix" class="el-input__icon icon_pwd"></i>
            </el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="验证码" name="second">
          <el-form-item prop="phone">
            <el-input
              ref="phone"
              maxlength="11"
              type="Number"
              v-model="loginInfo.phone"
              auto-complete="off"
              placeholder="请输入手机号"
              @keyup.enter.native="loginAction"
            >
              <i slot="prefix" class="el-input__icon icon_phone"></i>
            </el-input>
          </el-form-item>
          <el-form-item prop="msgCode" class="msgCode">
            <el-input
              ref="code"
              maxlength="6"
              type="Number"
              v-model="loginInfo.msgCode"
              auto-complete="off"
              placeholder="请输入验证码"
              @keyup.enter.native="loginAction"
            >
              <i slot="prefix" class="el-input__icon icon_code"></i>
            </el-input>
            <el-button
              type="primary"
              @click="sendcode"
              :disabled="disabled"
              v-if="disabled == false"
              :loading="msgCodeLoading"
            >{{ btntxt }}</el-button>
            <el-button
              type="primary"
              @click="sendcode"
              :disabled="disabled"
              v-if="disabled == true"
            >{{ btntxt }}</el-button>
          </el-form-item>
        </el-tab-pane>
      </el-tabs>

      <el-checkbox v-if="curIndex == 0" v-model="checked" checked class="remember">记住密码</el-checkbox>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="loginAction" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { requestLogin, getData } from "../../utils/networks/api";
import {
  setSession,
  removeSession,
  setUserid,
  removeUserid
} from "../../utils/networks/auth";
import md5 from "js-md5";
// import NProgress from 'nprogress'
import { LoginTabIndex, LoginKey, UserKey } from "@/utils/networks/api";

export default {
  data() {
    return {
      msgCodeLoading: false,
      logining: false,
      activeName: "first",
      disabled: false,
      time: 0,
      btntxt: "发送验证码",
      curIndex: 0,
      loginInfo: {
        phone: "",
        msgCode: "",
        account: "",
        checkPass: ""
      },
      rules1: {
        phone: [
          { required: false, message: "请输入手机号", trigger: "blur, change" }
        ],
        msgCode: [{ required: false, message: "请输入验证码", trigger: "blur" }]
      },
      rules2: {
        account: [{ required: false, message: "请输入账号", trigger: "blur" }],
        checkPass: [{ required: false, message: "请输入密码", trigger: "blur" }]
      },
      checked: true
    };
  },
  methods: {
    sendcode() {
      var _this = this;

      const reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
      if (this.loginInfo.phone == "") {
        this.$message({
          message: "手机号不能为空",
          center: true
        });
        _this.msgCodeLoading = false;
        return;
      }
      if (!reg.test(this.loginInfo.phone)) {
        this.$message({
          message: "请输入正确的手机号",
          center: true
        });
        return;
      } else {
        const param = {
          funcid: "1101",
          mobile: _this.loginInfo.phone
        };
        _this.msgCodeLoading = true;
        _this
          .getData(param)
          .then(res => {
            if (res.code == 1) {
              _this.$message({
                message: "发送成功",
                type: "success",
                center: true
              });
              _this.time = 60;
              _this.disabled = true;
              _this.timer();
            }
            _this.msgCodeLoading = false;
          })
          .catch(err => {
            console.log(err);
            _this.msgCodeLoading = false;
          });
      }
    },
    // 60S倒计时
    timer() {
      if (this.time > 0) {
        this.time--;
        this.btntxt = this.time + "s后重新获取";
        setTimeout(this.timer, 1000);
      } else {
        this.time = 0;
        this.btntxt = "重新发送";
        this.disabled = false;
      }
    },
    handleClick(tab) {
      this.curIndex = tab.index;
      localStorage.setItem(LoginTabIndex, String(this.curIndex));
    },
    handleReset2() {
      this.$refs.loginInfo.resetFields();
    },
    loginAction(ev) {
      var _this = this;
      this.$refs.loginInfo.validate(valid => {
        if (valid) {
          _this.logining = true;
          var params = {
            funcid: "1100",
            flag: this.curIndex
          };
          if (this.curIndex == 0) {
            params.username = _this.loginInfo.account;
            params.password = _this.CalcuMD5(_this.loginInfo.checkPass);
          } else {
            params.username = _this.loginInfo.phone;
            params.code = _this.loginInfo.msgCode;
          }
          _this
            .getData(params)
            .then(res => {
              // console.log(res)
              if (res.code == 1) {
                if (!_this.checked) {
                  _this.loginInfo.checkPass = "";
                }
                localStorage.setItem(LoginKey, JSON.stringify(_this.loginInfo));
                localStorage.setItem(UserKey, JSON.stringify(res.data));
                sessionStorage.setItem("user", JSON.stringify(res.data));

                setSession(res.data.session);
                setUserid(res.data.userid);
                _this.$root.userData = res.data;
                this.$router.push({ path: "/bannerlist" });
              } else {
                removeSession();
                removeUserid();
              }
              _this.logining = false;
            })
            .catch(err => {
              console.log(err);
              _this.logining = false;
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  },
  watch: {
    curIndex(val) {
      this.activeName = val == 0 ? "first" : "second";
    }
  },
  mounted() {
    // var cacheIndex = parseInt(localStorage.getItem(LoginTabIndex))
    // if (cacheIndex) {
    //   this.curIndex = parseInt(cacheIndex)
    // }
    this.btntxt = "发送验证码";
    try {
      var jsonStr = localStorage.getItem(LoginKey);
      if (jsonStr) {
        this.loginInfo = JSON.parse(jsonStr);
        this.loginInfo.msgCode = "";
      }
    } catch (err) {}
    this.$refs["account"].focus();
  }
};
</script>

<style lang="scss">
// 处理input type = number的上下箭头
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}

form {
  .msgCode {
    .el-input {
      width: 55%;
      border-radius: 0px;
      float: left;
      display: inline-block;
    }
    .el-button {
      width: 40%;
      // border-top-left-radius: 0px;
      // border-bottom-left-radius: 0px;
      border-left: 0px;
      float: left;
      margin-left: 5%;
      display: inline-block;
    }
  }
}
.wraper {
  position: absolute;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #252a2f;
}

.switch_bg .el-tabs__item {
  color: white;
}

.icon_phone {
  background: url(../../assets/img/icon_login_phone.png) no-repeat;
  background-size: contain;
}
.icon_phone:before {
  content: "\66ff";
  font-size: 16px;
  visibility: hidden;
}

.icon_user {
  background: url(../../assets/img/icon_user_header.png) no-repeat;
  background-size: contain;
}
.icon_user:before {
  content: "\66ff";
  font-size: 16px;
  visibility: hidden;
}

.icon_code {
  background: url(../../assets/img/icon_msg_code.png) no-repeat;
  background-size: contain;
}
.icon_code:before {
  content: "\66ff";
  font-size: 16px;
  visibility: hidden;
}

.icon_pwd {
  background: url(../../assets/img/icon_user_pwd.png) no-repeat;
  background-size: contain;
}
.icon_pwd:before {
  content: "\66ff";
  font-size: 16px;
  visibility: hidden;
}
.bgc-146082 {
  background-color: #146082;
}
.login-container {
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #252a2f;
  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    float: left;
    margin: 0px 0px 10px 0px;
  }
  .el-tabs {
    .el-tabs__nav-wrap::after {
      display: none;
    }
    .el-tabs__nav {
      .el-tabs__item {
        color: white;
      }
      .el-tabs__item.is-active {
        color: #146082;
      }
    }
  }
}
</style>


