<template>
  <div class="video_wraper">
    <div class="video_container">
      <video :src="videosrc" controls="controls" style="height:180px;width:auto;"></video>
    </div>
    <!-- <el-upload class="upload-demo"
                     accept="image/png, image/jpg"
                     :file-list="fileList"
                     :show-file-list="true"
                     :data="uploadData"
                     :headers='headers'
                     :before-upload="beforeUpload"
                     :on-success='uploadSuccess'
                     :on-error='uploadError'
    action="http://dtcollege.oss-cn-qingdao.aliyuncs.com/">-->
    <!--    :headers='headers' -->
    <el-upload
      id="video_upload"
      accept="video/MP4"
      :file-list="fileList"
      :show-file-list="true"
      :data="uploadData"
      :before-upload="beforeUpload"
      :on-success="uploadSuccess"
      :on-error="uploadError"
      action="http://dtcollege.oss-cn-qingdao.aliyuncs.com/"
    >
      <el-button size="small" type="primary">上传视频</el-button>
      <el-button size="small" @click.stop="del">删除</el-button>
      <div
        slot="tip"
        class="el-upload__tip tex-l"
        style="line-height:30px;margin-top:0px;margin-bottom:5px;"
      >支持上传MP4文件，且不超过3GB</div>
    </el-upload>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  props: {
    // 行高
    rowHeight: [String, Number],
    // 每行的个数
    column: {
      default: 3
    },
    videosrc: {
      type: String
    }
  },
  data: () => ({
    list: [],
    // videosrc: '',
    imgHeight: null,
    uploadData: {},
    fileList: [],
    fileInfo: {} // 服务器返回的文件信息
  }),
  computed: {},
  watch: {},
  computed: {
    headers() {
      return {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Request-Method': 'POST'
      }
    }
  },
  methods: {
    del() {
      var _this = this
      _this
        .$confirm('确认删除该视频吗?', '提示', {
          type: 'warning'
        })
        .then(() => {
          _this.$emit('onsuccess', '')
          _this.videosrc = ''
        })
        .catch(() => {})
    },
    async getExtraData(file) {
      var res = await this.getData({
        funcid: '1022',
        fileName: file.name
      })

      return res
    },
    beforeUpload(file) {
      var _this = this
      var filetype = file.type.toLowerCase()
      var filename = file.name
      const isLt5M = file.size / 1024 / 1024 / 1024 < 3
      if (!isLt5M) {
        this.$message.error(filename + ' 的上传文件大小超过3GB!')
        _.pull(this.fileList, file)
        return false
      }
      if (filetype.indexOf('mp4') == -1) {
        this.$message.error('视频格式错误')
        _.pull(this.fileList, file)
        return false
      }

      return new Promise(function(resolve, reject) {
        _this
          .getData({
            funcid: '1022',
            fileName: file.name
          })
          .then(res => {
            var data = res.data
            _this.fileInfo = data
            _this.uploadData = {
              key: data.dir + data.cloudname,
              policy: data.policy,
              OSSAccessKeyId: data.accessid,
              success_action_status: '200',
              signature: data.signature
            }
            resolve(_this.uploadData)
          })
      })
    },
    uploadSuccess(response, file, fileList) {
      var url =
        this.fileInfo.host + '/' + this.fileInfo.dir + this.fileInfo.cloudname
      this.$emit('onsuccess', url)
      this.fileList = []
    },
    uploadError(err, file, fileList) {
      this.$emit('onsuccess', '')
      this.fileList = []
    }
  },
  created() {},
  watch: {
    // sourceurl (val) {
    //   this.videosrc = val
    // }
  },
  mounted() {
    // if (this.sourceurl) {
    //   this.videosrc = this.sourceurl
    // }
  }
}
</script>

<style scoped>
#video_upload {
  margin-top: 5px;
  float: left;
  bottom: 0;
}
.video_container {
  margin-left: 0;
  display: flex;
}
</style>


