<template>
  <div class="navWraper">
    <el-row v-for="(route,index) in filterRoutes" :key="index">
      <div class="contain">
        <i :class="route.iconCls" class="icon"></i>
        <span class="title">{{route.name}}</span>
      </div>
      <router-link
        class="children bgColor"
        v-for="child in route.children"
        v-if="!child.hidden"
        :key="child.path"
        :to="child.path"
        tag="a"
        active-class="active"
      >
        <i :class="route.iconCls" class="icon"></i>
        <span class="title">{{child.name}}</span>
      </router-link>
    </el-row>
  </div>
</template>

<script>
export default {
  props: {
    // routes: Array // 所有路由
  },
  data() {
    return {
      routes: []
    }
  },
  methods: {},
  computed: {
    filterRoutes() {
      var showRoutes = []
      for (var i = 0; i < this.routes.length; i++) {
        var route = this.routes[i]
        if (route.name != '') {
          showRoutes.push(route)
        }
      }
      return showRoutes
    }
  },
  mounted() {
    this.routes = this.$router.options.routes
  }
}
</script>

<style lang="scss" scoped>
.bgColor {
  background-color: #2f353b;
}
a {
  text-decoration: none;
}

.active {
  background-color: #146487;
  text-decoration: none;
}
.navWraper {
  height: 100%;
  background-color: #252a2f;
  .contain {
    text-align: left;
    height: 44px;
    padding-left: 20px;
    .icon {
      color: #6e8399;
    }
    .title {
      font-size: 15px;
      margin-left: 5px;
      line-height: 44px;
      color: #6e8399;
    }
  }
  .children {
    display: block;
    text-align: left;
    height: 44px;
    padding-left: 20px;
    font-size: 14px;
    // background-color: #2f353b;
    .icon {
      visibility: hidden;
    }
    .title {
      margin-left: 5px;
      line-height: 44px;
      color: #f2f2f2;
    }
  }
}
</style>


