<template>
  <div class="mywraper">
    <!--编辑界面-->
    <el-dialog top="20px" title="选择课程" :visible.sync="visible" :close-on-click-modal="false">
      <el-radio-group style="padding-bottom:10px" v-model="type" size="medium">
        <el-radio-button :label="0">单课程</el-radio-button>
        <el-radio-button :label="1">系列课</el-radio-button>
      </el-radio-group>
      <!--列表-->
      <el-table
        ref="table"
        v-if="type == 0"
        class="dialog_body"
        :data="singleOption.data_list"
        stripe
        border
        highlight-current-row
        v-loading="singleOption.listLoading"
        @selection-change="selsChange"
        @select="select"
        style="width: 100%;"
      >
        <el-table-column type="selection" width="55" :selectable="checkboxInit"></el-table-column>
        <el-table-column prop="banner" label="封面" width="120">
          <template slot-scope="scope">
            <!-- // 9 / 16  -->
            <img
              style="margin-top:8px;min-height: 54px"
              :src="scope.row.banner"
              height="54px;"
              width="96px;"
              alt
            />
          </template>
        </el-table-column>
        <el-table-column prop="title" label="课程名"></el-table-column>
        <el-table-column prop="anchorname" label="主播" width="120"></el-table-column>
        <!-- sortable -->
        <el-table-column prop="clicknum" label="点击人数" width="60"></el-table-column>
        <el-table-column prop="buynum" label="购买人数" width="60"></el-table-column>
        <el-table-column prop="status" label="课程状态" width="80" :formatter="formatStatus"></el-table-column>
        <el-table-column prop="fee" label="价格" width="60"></el-table-column>
      </el-table>
      <el-table
        ref="table1"
        v-if="type == 1"
        class="dialog_body"
        :data="seriesOption.data_list"
        stripe
        border
        highlight-current-row
        v-loading="seriesOption.listLoading"
        @selection-change="selsChange"
        @select="select"
        style="width: 100%;"
      >
        <el-table-column type="selection" width="55" :selectable="checkboxInit"></el-table-column>
        <el-table-column prop="banner" label="封面" width="120">
          <template slot-scope="scope">
            <!-- // 9 / 16  -->
            <img
              style="margin-top:8px;min-height: 54px"
              :src="scope.row.banner"
              height="54px;"
              width="96px;"
              alt
            />
          </template>
        </el-table-column>
        <el-table-column prop="title" label="课程名"></el-table-column>
        <el-table-column prop="anchorname" label="主播" width="120"></el-table-column>
        <!-- sortable -->
        <el-table-column prop="clicknum" label="点击人数" width="60"></el-table-column>
        <el-table-column prop="buynum" label="购买人数" width="60"></el-table-column>
        <el-table-column prop="datastatus" label="课程状态" width="80" :formatter="formatDatastatus"></el-table-column>
        <el-table-column prop="fee" label="价格" width="60"></el-table-column>
      </el-table>
      <!--工具条-->
      <el-col v-show="type == 0" :span="24" class="toolbar">
        <el-pagination
          background
          layout="sizes,prev,pager,next,slot"
          @size-change="handleSizeChange1"
          @current-change="handleCurrentChange1"
          :current-page.sync="singleOption.curPage"
          :page-sizes="[10, 20, 30, 50]"
          :page-size="singleOption.pageSize"
          :total="singleOption.total"
          style="float:right;"
        >
          <span>&nbsp;共{{Math.ceil(singleOption.total/singleOption.pageSize)}}页</span>
        </el-pagination>
      </el-col>
      <el-col v-show="type == 1" :span="24" class="toolbar">
        <el-pagination
          background
          layout="sizes,prev,pager,next,slot"
          @size-change="handleSizeChange2"
          @current-change="handleCurrentChange2"
          :current-page.sync="seriesOption.curPage"
          :page-sizes="[10, 20, 30, 50]"
          :page-size="seriesOption.pageSize"
          :total="seriesOption.total"
          style="float:right;"
        >
          <span>&nbsp;共{{Math.ceil(seriesOption.total/seriesOption.pageSize)}}页</span>
        </el-pagination>
      </el-col>

      <div slot="footer" class="dialog-footer">
        <el-button @click.native="visible = false">取消</el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="addLoading">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ChooseCourse",
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      addLoading: false,
      visible: false,
      sels: [],
      type: 0, //0 单课 1 系列课
      singleOption: {
        listLoading: false,
        data_list: [],
        total: 0,
        pageSize: 10
      },
      seriesOption: {
        listLoading: false,
        data_list: [],
        total: 0,
        pageSize: 10
      }
    };
  },
  watch: {
    // 如果 `question` 发生改变，这个函数就会运行
    question(newQuestion, oldQuestion) {
      // this.answer = 'Waiting for you to stop typing...'
    },
    type(value) {
      if (value == 0) {
        this.requestSingleList();
      } else if (value == 1) {
        this.requestSeriesList();
      }
    },
    show(value) {
      this.visible = value;
    },
    visible(value) {
      this.$emit("visible", value);
    }
  },
  computed: {
    // 计算属性的 getter
    reversedMessage() {
      return "";
    }
  },
  methods: {
    checkboxInit(row, index) {
      console.log(row);
      if (index == -1) {
        //你需要判断的条件
        return 0; //不可勾选
      } else {
        return 1; //可勾选
      }
    },
    select(selection, row) {
      console.log(selection.length);
      console.log(
        Object.prototype.toString.call(selection[0]) === "[object Object]"
      );
      if (this.$refs.table) {
        this.$refs.table.clearSelection();
      }
      if (this.$refs.table1) {
        this.$refs.table1.clearSelection();
      }

      if (selection.length === 0) {
        // 判断selection是否有值存在
        return;
      }
      console.log("点击", selection, row);
      if (this.$refs.table) {
        this.$refs.table.toggleRowSelection(row, true);
      }
      if (this.$refs.table1) {
        this.$refs.table1.toggleRowSelection(row, true);
      }
    },
    formatType: function(row, column) {
      if (row.type == 0) {
        return "PPT直播";
      } else if (row.type == 1) {
        return "课程录播";
      } else if (row.type == 2) {
        return "课程直播";
      }
      return "未知";
    },
    formatStatus: function(row, column) {
      if (row.status == 0) {
        return "即将开始";
      } else if (row.status == 1) {
        return "直播中";
      } else if (row.status == 2) {
        return "直播结束";
      }
      return "未知";
    },
    formatDatastatus: function(row, column) {
      if (row.datastatus == 0) {
        return "已删除";
      } else if (row.datastatus == 1) {
        return "已上架";
      } else if (row.datastatus == 2) {
        return "已下架";
      } else if (row.datastatus == -1) {
        return "已隐藏";
      }
      return "未知";
    },
    requestSingleList() {
      var _this = this;
      _this.singleOption.listLoading = true;
      _this
        .getData({
          funcid: "1111",
          datastatus: "1",
          page: _this.singleOption.curPage,
          pagesize: _this.singleOption.pageSize
        })
        .then(res => {
          if (res.code == 1) {
            _this.singleOption.data_list = res.data.records;
            _this.singleOption.curPage = res.data.page;
            _this.singleOption.pageSize = res.data.pagesize;
            _this.singleOption.total = res.data.totalcount;
          }
          _this.singleOption.listLoading = false;
        });
    },
    requestSeriesList() {
      var _this = this;
      _this.seriesOption.listLoading = true;
      _this
        .getData({
          funcid: "1112",
          datastatus: "1",
          page: _this.seriesOption.curPage,
          pagesize: _this.seriesOption.pageSize
        })
        .then(res => {
          if (res.code == 1) {
            _this.seriesOption.data_list = res.data.records;
            _this.seriesOption.curPage = res.data.page;
            _this.seriesOption.pageSize = res.data.pagesize;
            _this.seriesOption.total = res.data.totalcount;
          }
          _this.seriesOption.listLoading = false;
        });
    },
    handleSizeChange1(value) {
      this.singleOption.pageSize = value;
      this.requestSingleList();
    },
    handleCurrentChange1(value) {
      this.singleOption.curPage = value;
      this.requestSingleList();
    },
    handleSizeChange2(value) {
      this.seriesOption.pageSize = value;
      this.requestSeriesList();
    },
    handleCurrentChange2(value) {
      this.seriesOption.curPage = value;
      this.requestSeriesList();
    },
    selsChange(sels) {
      this.sels = sels;
    },
    editSubmit() {
      if (this.sels.length == 0) {
        this.$message.error("请选择课程!");
        return;
      }
      this.$emit("commit", this.sels[0]);
      this.visible = false;
    }
  },
  created() {},
  activated() {},
  mounted() {
    _.forEach([1, 3], function(value, key) {
      console.log(key, value);
    });
    // this.visible = this.show;
    this.requestSingleList();
  }
};
</script>
>

<style lang="scss" scoped>
/deep/ .el-dialog__body {
  padding-top: 0px;
}
/deep/ .el-table td {
  padding: 0px;
}

/deep/ .has-gutter .el-checkbox {
  display: none;
}
.dialog_body {
  max-height: 350px;
  overflow: hidden;
  overflow-y: scroll;
}

.mywraper {
  color: white;
}

.toolbar {
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>


