import VueRouter from 'vue-router'

import Login from '../views/common/Login.vue'
import NotFound from '../views/common/404.vue'
import Home from '../views/Home.vue'

import OnlineRoomlist from '../views/common/OnlineRoomlist'
import Courselist from '../views/courseNav/Courselist'

import RoomSetting from '../views/studioNav/RoomSetting'
import AccountList from '../views/studioNav/AccountList'
import StatisticsList from '../views/statisticsNav/StatisticsList'
import StatisticsDetail from '../views/statisticsNav/StatisticsDetail'

import SingleCourse from '../views/courseNav/course/EdtSingleCourse'
import SeriesCourse from '../views/courseNav/course/EdtSeriesCourse'
import SingleListOfSeries from '../views/courseNav/course/EdtSingleListOfSeries'

import BannerList from '../views/HomePage/BannerList'
import BannerDetail from '../views/HomePage/BannerDetail'
import HomeModule from '../views/HomePage/HomeModule'
import BoutiqueCourseList from '../views/HomePage/BoutiqueCourseList'
import BoutiqueDetail from '../views/HomePage/BoutiqueDetail'
import ConsuleList from '../views/CounselPage/ConsuleList'
import ConsuleDetail from '../views/CounselPage/ConsuleDetail'
import ConsuleEdite from '../views/CounselPage/ConsuleEdite'

import ChooseCourse from '../components/Course/chooseCourse'

// 定义的所有页面 SingleCourse
var routes = [
  {
    path: '/singlecourse',
    component: SingleCourse,
    name: '',
    hidden: true
  },
  {
    path: '/login',
    component: Login,
    name: '',
    hidden: true
  },
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  {
    path: '/',
    component: Home,
    name: '首页管理',
    iconCls: 'fa fa-home', // 图标样式class
    children: [
      {
        path: '/bannerlist',
        component: BannerList,
        name: '首页轮播图',
        meta: {
          keepAlive: true
        },
        children: [
          {
            path: 'bannerdetail',
            component: BannerDetail,
            name: '编辑Banner'
          }
        ]
      },
      {
        path: '/homemodule',
        component: HomeModule,
        name: '首页模块排序',
        meta: {
          keepAlive: false
        }
      },
      {
        path: '/boutiquecourselist',
        component: BoutiqueCourseList,
        name: '热门精品',
        meta: {
          keepAlive: true
        },
        children: [
          {
            path: 'boutiquedetail',
            component: BoutiqueDetail,
            name: '编辑热门精品课程'
          }
        ]
      }
    ]
  },
  {
    path: '/',
    component: Home,
    name: '资讯管理',
    iconCls: 'fa fa-book',
    children: [
      {
        path: '/consulelist',
        component: ConsuleList,
        name: '资讯列表',
        children: [
          {
            path: 'consuledetail',
            component: ConsuleDetail,
            name: '资讯详情'
          },
          {
            path: 'consuleedite',
            component: ConsuleEdite,
            name: '编辑资讯'
          }
        ]
      }
    ]
  },
  {
    path: '*',
    hidden: true,
    redirect: {
      path: '/404'
    }
  }
]

var router = new VueRouter({
  routes: routes
})

router.beforeEach((to, from, next) => {
  if (to.path == '/login') {
    sessionStorage.removeItem('user')
  }
  const user = JSON.parse(sessionStorage.getItem('user'))
  if (!user && to.path != '/login') {
    next({
      path: '/login'
    })
  } else {
    next()
  }
})

export default router



