<template>
  <div class="bd_wraper">
    <el-breadcrumb class="app-breadcrumb" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item v-for="(item)  in levelList" :key="item.path">
        <!-- :to="item.redirect||item.path -->
        <!-- @click="clickCurrentPath(item) -->
        <router-link
          v-if="item.name"
          :to="{path:item.path,query:getQuery(item)} "
        >{{getTitle(item)}}</router-link>
        <!-- <span @click="clickCurrentPath(item)">{{getTitle(item)}}</span> -->
      </el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-button
      v-if="$root.topbarbtntitle"
      class="right_btn"
      type="primary"
      size="small"
      @click.native="clickAction"
    >{{$root.topbarbtntitle}}</el-button>
  </div>
</template>

<script>
import Global from '../components/tool/Global'
export default {
  name: 'breadCrumb',
  props: {
    rightTitle: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      topbarBtnTitle: '',
      levelList: null
    }
  },
  methods: {
    clickCurrentPath(item) {
      this.$router.push({ path: item.redirect || item.path, query: item.query })
    },
    /**
     * 生成面包屑的方法
     */
    getBreadcrumb() {
      var _this = this
      _this.$nextTick(() => {
        const matched = _this.$route.matched.filter(item => item.name)

        _this.levelList = matched
      })
    },
    getQuery(item) {
      var index = this.levelList.indexOf(item)
      if (
        this.$route.query &&
        (index == this.levelList.length - 1 || index == 0)
      ) {
        return this.$route.query
      }
      return {}
    },
    getTitle(item) {
      var index = this.levelList.indexOf(item)
      if (
        this.$route.query &&
        this.$route.query.title &&
        index == this.levelList.length - 1
      ) {
        return this.$route.query.title
      }
      return item.name
    },
    clickAction() {
      this.$root.GlobalEvent.$emit('clickRightBtn')
    }
  },
  computed: {},
  mounted() {
    this.$nextTick(() => {
      this.getBreadcrumb()
    })
  },
  watch: {
    $route(to, from) {
      this.getBreadcrumb()
    }
  }
}
</script>

<style scoped>
.bd_wraper {
  height: 30px;
  margin-bottom: 5px;
  border-bottom: 1px solid #f0f0f0;
}
.app-breadcrumb {
  display: inline-block;
  float: left;
}
.right_btn {
  float: right;
  margin-top: -8px;
  right: 35px;
}
</style>


