<template>
  <div class="single_wraper">
    <el-form :rules="rules" ref="courseinfo" :model="course" label-width="100px">
      <!-- <el-form-item label="课程分类">
        <el-select class="input_common"
                   v-model="course.classtype"
                   placeholder="请选择活动区域">
          <el-option label="区域一"
                     value="shanghai"></el-option>
          <el-option label="区域二"
                     value="beijing"></el-option>
        </el-select>
      </el-form-item>-->
      <el-form-item label="课程分类" prop="tag">
        <el-select v-model="course.tag" placeholder="请选择" class="input_common">
          <el-option
            v-for="item in tagList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="课程名称" prop="title">
        <el-input
          maxlength="20"
          class="input_common"
          v-model="course.title"
          placeholder="请输入课程标题,最多20字"
        ></el-input>
      </el-form-item>
      <!-- 讲师头像 -->
      <el-form-item label="讲师头像" prop="banner1" style="width:80%;min-width:600px;">
        <div class="head_icon">
          <img width="auto" height="120px" :src="course.banner1" :onerror="errorImg" />
        </div>
        <el-upload
          class="upload-demo"
          accept="image/png, image/jpg"
          action
          :auto-upload="false"
          :show-file-list="false"
          :limit="1"
          :file-list="fileList"
          :on-change="changeUpload1"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">支持上传jpg/png文件，且不超过5M</div>
        </el-upload>
      </el-form-item>
      <!-- 课程封面 -->
      <el-form-item label="课程封面" prop="banner" style="width:80%;min-width:600px;">
        <div class="head_icon">
          <img width="auto" height="120px" :src="course.banner" :onerror="errorImg" />
        </div>
        <el-upload
          class="upload-demo"
          accept="image/png, image/jpg"
          action
          :auto-upload="false"
          :show-file-list="false"
          :limit="1"
          :file-list="fileList"
          :on-change="changeUpload"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">支持上传jpg/png文件，且不超过5M</div>
        </el-upload>
      </el-form-item>
      <!-- 幻灯片 -->
      <el-form-item v-if="course.type == 0" label="添加幻灯片" prop="ppt">
        <el-col>
          <photo-drag-wraper :list="course.ppt" rowHeight="80" column="6"></photo-drag-wraper>
        </el-col>
      </el-form-item>
      <el-form-item v-else-if="course.type == 1" label="添加视频" prop="ppt">
        <video-upload
          @onsuccess="refreshVideo"
          :videosrc="course.ppt[0]&&course.ppt[0].url?course.ppt[0].url:''"
        ></video-upload>
      </el-form-item>

      <el-form-item label="课程简介" prop="comment">
        <el-input
          class="input_common"
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 8}"
          placeholder="请输入内容"
          v-model="course.comment"
        ></el-input>
        <span class>111</span>
      </el-form-item>

      <el-form-item label="简介图片" prop="detail">
        <el-col>
          <photo-drag-wraper
            :uploadTitle="uploadDetailTitle"
            :list="course.detail"
            rowHeight="80"
            column="6"
          ></photo-drag-wraper>
        </el-col>
      </el-form-item>

      <el-form-item label="课程费用" prop="fee">
        <el-input type="Number" class="input_common" v-model="course.fee"></el-input>
        <span class="fl fc_333333 mal-10">元</span>
        <span class="fl fc_888888">&nbsp;(0为免费)</span>
      </el-form-item>
      <el-form-item label="课程时间" required>
        <el-form-item prop="begintime" class="block">
          <el-date-picker
            type="datetime"
            style="width:210px"
            value-format="yyyy-MM-dd HH:mm:ss"
            v-model="course.begintime"
            :picker-options="pickerOptionsStart"
            @change="changeEnd"
            placeholder="开始时间"
          ></el-date-picker>
        </el-form-item>
        <span class="fc-333">至</span>
        <span class="block">
          <el-form-item prop="autoendtime">
            <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              style="width:210px"
              v-model="course.autoendtime"
              :picker-options="pickerOptionsEnd"
              @change="changeStart"
              type="datetime"
              placeholder="结束时间"
            ></el-date-picker>
          </el-form-item>
        </span>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit" :loading="saveloading">保存</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </el-form-item>
    </el-form>

    <!-- vueCropper 剪裁图片实现-->
    <el-dialog title="图片剪裁" :visible.sync="dialogVisible" append-to-body>
      <div class="cropper-content">
        <div class="cropper" style="text-align:center">
          <!-- @realTime="realTime" -->
          <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="true"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
          ></vueCropper>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false;loading=false">取 消</el-button>
        <el-button type="primary" @click="finish(option.fixedNumber)" :loading="loading">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import photoDragWraper from "@/components/photoDragWraper";
import VideoUpload from "@/components/VideoUpload";
import Vue from "vue";
export default {
  components: {
    photoDragWraper,
    VideoUpload
  },
  data() {
    var _this = this;
    return {
      uploadDetailTitle: "上传简介图片",
      pickerOptionsStart: {},
      pickerOptionsEnd: {},
      saveloading: false,
      dialogVisible: false,
      // 裁剪组件的基础配置option
      option: {
        img: "", // 裁剪图片的地址
        info: true, // 裁剪框的大小信息
        outputSize: 0.8, // 裁剪生成图片的质量
        outputType: "jpeg", // 裁剪生成图片的格式
        canScale: false, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        // autoCropWidth: 320, // 默认生成截图框宽度
        // autoCropHeight: 180, // 默认生成截图框高度
        fixedBox: false, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [16, 9], // 截图框的宽高比例
        full: true, // 是否输出原图比例的截图
        canMoveBox: true, // 截图框能否拖动
        original: false, // 上传图片按照原始比例渲染
        centerBox: true, // 截图框是否被限制在图片里面
        infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      },
      // 防止重复提交
      loading: false,
      errorImg: 'this.src="' + require("@/assets/img/default_nodata.png") + '"',
      fileList: [],
      isEdited: false,
      tagList: [],
      course: {
        tag: "0",
        signleid: "",
        type: "1", // 0 PPT  1 录播  2 直播
        classtype: "",
        title: "",
        banner: "",
        banner1: "",
        comment: "",
        fee: "0.00",
        begintime: "",
        autoendtime: "",
        discount: "",
        ppt: [],
        detail: []
      },
      rules: {
        title: [
          { required: true, message: "请输入课程标题", trigger: "change" }
        ],
        tag: [{ required: true, message: "请输入课程分类", trigger: "change" }],
        banner: [
          {
            required: true,
            message: "请选择课程封面",
            trigger: "blur"
          }
        ],
        ppt: [
          {
            required: true,
            message: "请上传PPT",
            trigger: "change"
          }
        ],
        comment: [
          { required: true, message: "请填写课程简介", trigger: "change" }
        ],
        fee: [{ required: true, message: "请输入课程费用", trigger: "change" }],
        begintime: [
          { required: true, message: "请选择开始时间", trigger: "change" }
        ],
        autoendtime: [
          { required: true, message: "请选择结束时间", trigger: "change" }
        ]
      }
    };
  },
  watch: {
    // 如果 `question` 发生改变，这个函数就会运行
    question(newQuestion, oldQuestion) {
      // this.answer = 'Waiting for you to stop typing...'
    },
    course: {
      handler(newValue, oldValue) {
        this.isEdited = true;
      },
      deep: true
    },
    "course.ppt"(newVal) {
      if (this.course.ppt && this.course.type != 2) {
        setTimeout(() => {
          this.$refs.courseinfo.validateField("ppt");
        }, 0);
      }
    }
  },
  computed: {
    // 计算属性的 getter
    reversedMessage() {
      return "";
    }
    // rules() {
    //   return {
    //     title: [
    //       { required: true, message: '请输入课程标题', trigger: 'change' }
    //     ],
    //     banner: [
    //       {
    //         required: true,
    //         message: '请选择课程封面',
    //         trigger: 'blur'
    //       }
    //     ],
    //     ppt: [
    //       {
    //         type: 'array',
    //         required: true,
    //         message: _this.course.type == 0 ? '请上传PPT' : '请上传录播课程',
    //         trigger: 'change'
    //       }
    //     ],
    //     comment: [
    //       { required: true, message: '请填写课程简介', trigger: 'change' }
    //     ],
    //     fee: [{ required: true, message: '请输入课程费用', trigger: 'change' }],
    //     begintime: [
    //       { required: true, message: '请选择开始时间', trigger: 'change' }
    //     ],
    //     autoendtime: [
    //       { required: true, message: '请选择结束时间', trigger: 'change' }
    //     ]
    //   }
    // }
  },
  methods: {
    getCourseTagList(callback) {
      var _this = this;
      _this.getData({ funcid: 1029 }).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          var records = res.data.records;
          for (let i = 0; i < records.length; i++) {
            var item = records[i];
            _this.tagList.push({ value: item.tagid, label: item.tagname });
          }
          _this.tagList.unshift({ value: "", label: "全部" });
        }
        if (callback) {
          callback();
        }
        _this.listLoading = false;
      });
    },
    refreshSelDate(isBegin) {
      var begintime = this.course.begintime;
      var endtime = this.course.autoendtime;
      if (begintime && endtime) {
        var beginDate = new Date(begintime);
        var endDate = new Date(endtime);
        if (beginDate.getTime() > endDate.getTime()) {
          if (isBegin) {
            this.course.autoendtime = begintime;
          } else {
            this.course.begintime = endtime;
          }
        }
      }
    },
    changeStart() {
      var _this = this;
      _this.refreshSelDate(false);
      this.pickerOptionsStart = Object.assign({}, this.pickerOptionsStart, {
        disabledDate: time => {
          var endtime = this.course.autoendtime;
          if (!endtime) {
            return;
          }
          endtime = endtime.split(" ")[0] + " 00:00:00";
          var endDate = new Date(endtime);
          return time.getTime() > endDate.getTime();
        }
      });
    },
    changeEnd() {
      var _this = this;
      _this.refreshSelDate(true);
      this.pickerOptionsEnd = Object.assign({}, this.pickerOptionsEnd, {
        disabledDate: time => {
          var starttime = this.course.begintime;
          if (!starttime) {
            return;
          }
          starttime = starttime.split(" ")[0] + " 00:00:00";
          var startDate = new Date(starttime);
          return time.getTime() < startDate.getTime();
        }
      });
    },
    // 获取课程列表
    refreshData() {
      var _this = this;
      const para = {
        funcid: "1012",
        anchorid: this.room.anchorid,
        condition: this.condition,
        starttime: this.starttime,
        endtime: this.endtime,
        datastatus: this.courseState,
        page: this.showPage,
        pagesize: this.showPageSize
      };
      _this.listLoading = true;
      _this.getData(para).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          // 单课程
          if (_this.curIndex == 0) {
            _this.pageInfo1.curPage = res.data.page;
            _this.pageInfo1.pageSize = res.data.pagesize;
            _this.pageInfo1.total = res.data.totalcount;

            // 系列课
          } else {
            _this.pageInfo2.curPage = res.data.page;
            _this.pageInfo2.pageSize = res.data.pagesize;
            _this.pageInfo2.total = res.data.totalcount;
          }

          _this.data_list = res.data.records;
        }
        _this.listLoading = false;
      });
    },
    refreshVideo(src) {
      if (!src || src == "") {
        this.course.ppt = [];
      } else {
        this.course.ppt = [{ url: src }];
      }
    },
    // 上传按钮   限制图片大小
    changeUpload(file, fileList) {
      this.option.fixedNumber = [16, 9];
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        this.$message.error("上传文件大小不能超过 5MB!");
        this.fileList = [];
        return false;
      }
      var filetype = file.raw.type.toLowerCase();
      if (
        filetype.indexOf("jpg") == -1 &&
        filetype.indexOf("jpeg") == -1 &&
        filetype.indexOf("png") == -1
      ) {
        this.$message.error("图片类型错误");
        this.fileList = [];
        return false;
      }

      this.fileinfo = file;
      const imgurl = URL.createObjectURL(file.raw);
      // 上传成功后将图片地址赋值给裁剪框显示图片
      this.$nextTick(() => {
        this.option.img = imgurl;
        this.dialogVisible = true;
      });
      this.fileList = [];
    },
    changeUpload1(file, fileList) {
      this.option.fixedNumber = [1, 1];
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        this.$message.error("上传文件大小不能超过 5MB!");
        this.fileList = [];
        return false;
      }
      var filetype = file.raw.type.toLowerCase();
      if (
        filetype.indexOf("jpg") == -1 &&
        filetype.indexOf("jpeg") == -1 &&
        filetype.indexOf("png") == -1
      ) {
        this.$message.error("图片类型错误");
        this.fileList = [];
        return false;
      }

      this.fileinfo = file;
      const imgurl = URL.createObjectURL(file.raw);
      // 上传成功后将图片地址赋值给裁剪框显示图片
      this.$nextTick(() => {
        this.option.img = imgurl;
        this.dialogVisible = true;
      });
      this.fileList = [];
    },
    // 点击裁剪，这一步是可以拿到处理后的地址
    finish(ratio) {
      var _this = this;
      this.$refs.cropper.getCropBlob(data => {
        var fileName = "goods" + _this.fileinfo.uid;
        this.loading = true;
        // 上传阿里云服务器
        setTimeout(() => {
          // var file = _this.convertBase64UrlToBlob(data)
          const imgurl = URL.createObjectURL(data);
          const param = new FormData(); // 创建form对象
          param.append("file", data, _this.fileinfo.name); // 通过append向form对象添加数据
          param.append("filetype", "jpeg");
          if (_this.IsArrayEqual(ratio, [16, 9])) {
            _this.uploadImage(param).then(res => {
              if (res.code == 1) {
                console.log(res.list.url);
                _this.course.banner = res.list.url;
              }
              _this.loading = false;
              _this.dialogVisible = false;
              _this.$refs.courseinfo.validateField("banner");
            });
          } else if (_this.IsArrayEqual(ratio, [1, 1])) {
            _this.uploadImage(param).then(res => {
              if (res.code == 1) {
                console.log(res.list.url);
                _this.course.banner1 = res.list.url;
              }
              _this.loading = false;
              _this.dialogVisible = false;
              _this.$refs.courseinfo.validateField("banner1");
            });
          }
        }, 3000);
      });
    },
    requestCourseDetail() {
      var _this = this;
      this.getData({
        funcid: "1005",
        signleid: this.course.signleid
      }).then(res => {
        if (res.code == 1) {
          _this.course = res.data;
          if (!_this.course.detail) {
            _this.course.detail = [];
          }
          if (!_this.course.ppt) {
            _this.course.ppt = [];
          }
          setTimeout(() => {
            _this.isEdited = false;
          }, 0);
        }
      });
    },
    cancelAction() {
      var _this = this;
      if (_this.course.signleid && _this.isEdited) {
        _this
          .$confirm("是否保存修改?", "提示", {
            type: "warning"
          })
          .then(() => {
            _this.onSubmit();
          })
          .catch(() => {
            _this.goback();
          });
      } else {
        _this.goback();
      }
    },
    goback() {
      this.$router.back(-1);
    },
    onSubmit() {
      var _this = this;
      _this.$refs.courseinfo.validate(valid => {
        console.log(valid);
        _this.saveloading = true;
        if (valid) {
          var isNew = !this.course.signleid;
          var param = _this.course;
          param.funcid = "1006";

          _this
            .getData(param)
            .then(res => {
              if (res.code == 1) {
                _this.$message({
                  showClose: true,
                  message: this.course.signleid ? "编辑成功" : "新增成功",
                  type: "success"
                });
                _this.goback();
              }
              _this.saveloading = false;
            })
            .catch(error => {
              _this.saveloading = false;
            });
        } else {
          // _this.$message({
          //   showClose: true,
          //   message: '请填写必填项',
          //   type: 'error'
          // })
          _this.saveloading = false;
        }
      });
    },
    handlePreview() {},
    handleRemove() {},
    beforeRemove() {},
    handleExceed() {}
  },
  created() {},
  activated() {},
  mounted() {
    var _this = this;
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = "";
    });

    this.course.signleid = this.$route.query.signleid;
    this.course.type = this.$route.query.type;
    if (this.course.type == 1) {
      this.rules.ppt[0].message = "请上传视频录播";
    }

    this.getCourseTagList(() => {
      if (_this.course.signleid) {
        _this.requestCourseDetail();
      }
    });
  }
};
</script>


<style lang="scss" scoped>
.single_wraper {
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
  text-align: left;
}

.head_icon {
  // position: absolute;
  float: left;
  left: 10px;
  display: inline-block;
}
.upload-demo {
  display: inline-block;
  padding-top: 50px;
  margin-left: 20px;
}
.content {
  text-align: left;
}
.el-upload__tip {
  margin-left: 8px;
  display: inline-block;
}

.input_common {
  width: 280px;
  float: left;
}
.fl {
  float: left;
}
.fc_888888 {
  color: #888888;
}
.fc_333333 {
  color: #333333;
}

/deep/ .el-input__inner {
  height: 30px;
  line-height: 30px;
  border-radius: 2px;
}
/deep/ .el-input__icon {
  line-height: 30px;
}
/deep/ .el-textarea__inner {
  border-radius: 2px;
}
// 截图
.cropper-content {
  .cropper {
    width: auto;
    height: 300px;
  }
}
</style>


