<template>
  <section v-if="$route.path != rootPath">
    <router-view></router-view>
  </section>
  <section v-else>
    <!--工具条-->
    <div class="topbar">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="单次课" name="first"></el-tab-pane>
        <el-tab-pane label="系列课" name="seconds"></el-tab-pane>
      </el-tabs>
      <!-- <el-select style="width:100px"
                 v-model="value"
                 placeholder="请选择">
        <el-option v-for="item in options"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
        </el-option>
      </el-select>-->
      <div style="float:right;font-size:14px;" class="date_area">
        <span v-if="curIndex == 0">&nbsp;课程分类</span>
        <el-select v-if="curIndex == 0" style="width:90px;" v-model="tag" placeholder="请选择">
          <el-option
            v-for="item in tagList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <span v-if="curIndex == 0">
          开播时间
          <el-date-picker
            value-format="yyyy-MM-dd HH:mm:ss"
            style="width:150px"
            v-model="starttime"
            type="date"
            :picker-options="pickerOptionsStart"
            @change="changeEnd"
            placeholder="选择日期时间"
          ></el-date-picker>&nbsp;至
          <el-date-picker
            value-format="yyyy-MM-dd HH:mm:ss"
            style="width:150px"
            v-model="endtime"
            type="date"
            :picker-options="pickerOptionsEnd"
            @change="changeStart"
            placeholder="选择日期时间"
          ></el-date-picker>
        </span>
        <span>&nbsp;上架状态</span>
        <el-select style="width:90px;" v-model="courseState" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-input
          style="width:180px"
          autosize
          placeholder="请输入课程名称"
          v-model="condition"
          @keyup.enter.native="refreshData"
        ></el-input>
        <el-button style="margin-top:-2px;" type="primary" size="small" v-on:click="refreshData">查询</el-button>
      </div>
    </div>
    <!--列表-->
    <el-table
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="banner" label="封面" width="120">
        <template slot-scope="scope">
          <!-- // 9 / 16  -->
          <img
            style="margin-top:8px;min-height:54px;"
            :src="scope.row.banner"
            height="54px"
            width="96px"
            alt
          />
        </template>
      </el-table-column>
      <el-table-column prop="title" label="课程名称" width="120"></el-table-column>
      <el-table-column
        v-if="curIndex == 0"
        prop="tag"
        label="课程分类"
        width="90"
        :formatter="formatTag"
      ></el-table-column>
      <el-table-column
        v-if="curIndex == 0"
        prop="type"
        label="类型"
        width="100"
        :formatter="formatType"
      ></el-table-column>
      <el-table-column
        v-if="curIndex == 0"
        prop="type"
        label="直播状态"
        width="100"
        :formatter="formatStatus"
      ></el-table-column>
      <el-table-column prop="anchorname" label="主播" width="100"></el-table-column>
      <el-table-column prop="fee" label="费用" min-width="80"></el-table-column>
      <el-table-column v-if="curIndex == 0" prop="clicknum" label="参加人数" min-width="80"></el-table-column>
      <el-table-column v-else prop="buynum" label="参加人数" min-width="80"></el-table-column>
      <el-table-column v-if="curIndex == 0" prop="begintime" label="开播时间" min-width="110" sortable></el-table-column>
      <el-table-column
        v-if="curIndex == 0"
        prop="autoendtime"
        label="结束时间"
        min-width="110"
        sortable
      ></el-table-column>
      <!-- :formatter="formatDatastatus" -->
      <el-table-column prop="datastatus" label="上架状态" min-width="70">
        <template slot-scope="scope">
          <span
            :class="[scope.row.datastatus == 2 ? 'fc-ff3a3a':'']"
          >{{formatDatastatus(scope.row,scope.column)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <el-button
            v-if="curIndex == 0&&scope.row.type==2"
            type="text"
            size="small"
            @click="handleEdit(scope.$index, scope.row,'push')"
          >推流</el-button>
          <span v-if="curIndex == 0&&scope.row.type==2">|</span>

          <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row,'edit')">修改</el-button>
          <span>|</span>
          <el-button
            v-if="curIndex == 1"
            type="text"
            size="small"
            @click="handleEdit(scope.$index, scope.row,'manager')"
          >课程管理</el-button>
          <span v-if="curIndex == 1">|</span>
          <el-button
            type="text"
            size="small"
            @click="handleEdit(scope.$index, scope.row,'status')"
          >{{scope.row.datastatus == 1?'下架':'上架'}}</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        background
        layout="sizes,prev,pager,next,slot"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="showPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="showPageSize"
        :total="showTotal"
        style="float:right;"
      >
        <span>&nbsp;共{{showTotalPage}}页</span>
      </el-pagination>
    </el-col>

    <el-dialog top="200px" title="选择课程类型" :visible.sync="dialogFormVisible">
      <div class="class_wraper">
        <div v-for="(item,index) in type_list" class="room_item" :key="index">
          <div class="item_container" @click="clickItem(item)">
            <img v-if="index == 0" src="../../assets/img/icon_video_ppt.png" alt />
            <img v-else-if="index == 1" src="../../assets/img/icon_video_online.png" alt />
            <img v-else-if="index == 2" src="../../assets/img/icon_video_record.png" alt />
            <div class="item_title">{{item.title}}</div>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer"></div>
    </el-dialog>

    <el-dialog top="200px" title="推流地址" :visible.sync="pushDialogVisible">
      <div class="push_wraper">
        <div class="fz-14" style="display: flex;">
          <div style="min-width: 70px; margin-top: 10px;">推流地址:</div>
          <div style="margin-top: 10px; flex: 1 1 0%; display: flex; flex-direction: column;">
            <div id="push_url" style="text-align:left;">{{pushurl}}</div>
            <div
              style="text-align:left;margin-top: 10px; border-bottom: 1px solid rgb(241, 241, 241);"
            ></div>
            <span
              style="text-align:left;margin-top: 10px; color: rgb(136, 136, 136);"
            >注:该地址获取30分钟内有效,超时后无法推流,请重新获取</span>
          </div>
        </div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" v-clipboard:copy="pushurl" v-clipboard:success="onCopy">复制链接</el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import Vue from "vue";
// import NProgress from 'nprogress'

export default {
  data() {
    return {
      pushDialogVisible: false,
      dialogFormVisible: false,
      pushurl: "",
      user: {}, // 用户
      room: {}, // 直播间
      rootPath: "/courselist",
      curIndex: 0,
      activeName: "first",
      type_list: [
        {
          title: "幻灯片直播",
          imagesrc: "../../assets/img/icon_video_ppt.png",
          type: 0
        },
        {
          title: "视频录播",
          imagesrc: "../../assets/img/icon_video_online.png",
          type: 1
        },
        {
          title: "视频直播",
          imagesrc: "../../assets/img/icon_video_record.png",
          type: 2
        }
      ],
      data_list: [],

      pickerOptionsStart: {},
      pickerOptionsEnd: {},
      classtype: "",
      starttime: "",
      endtime: "",
      courseState: "",
      tag: "",
      condition: "",
      tagList: [],
      options: [
        {
          value: "",
          label: "全部"
        },
        // {
        //   value: '0',
        //   label: '已删除'
        // },
        {
          value: "1",
          label: "已上架"
        },
        {
          value: "2",
          label: "已下架"
        }
        // , {
        //   value: '-1',
        //   label: '已隐藏'
        // }
      ],

      pageInfo1: {
        pageSize: 10,
        curPage: 1,
        total: 1
      },
      pageInfo2: {
        pageSize: 10,
        curPage: 1,
        total: 1
      },
      listLoading: false
    };
  },
  methods: {
    changeStart() {
      this.pickerOptionsStart = Object.assign({}, this.pickerOptionsStart, {
        disabledDate: time => {
          if (!this.endtime) {
            return;
          }
          return time.getTime() > new Date(this.endtime);
        }
      });
    },
    changeEnd() {
      this.pickerOptionsEnd = Object.assign({}, this.pickerOptionsEnd, {
        disabledDate: time => {
          if (!this.starttime) {
            return;
          }
          return time.getTime() < new Date(this.starttime);
        }
      });
    },
    requestPushUrl(item) {
      var _this = this;
      _this
        .getData({
          funcid: "1025",
          signleid: item.signleid
        })
        .then(res => {
          if (res.code == 1) {
            _this.pushurl = res.data.url;
          }
        });
    },
    onCopy() {
      this.$message({
        showClose: true,
        message: "复制成功",
        type: "success"
      });
    },
    handleSizeChange(value) {
      if (this.curIndex == 0) {
        this.pageInfo1.curPage = 1;
        this.pageInfo1.pageSize = value;
      } else {
        this.pageInfo2.curPage = 1;
        this.pageInfo2.pageSize = value;
      }
      this.refreshData();
    },
    handleCurrentChange(value) {
      if (this.curIndex == 0) {
        this.pageInfo1.curPage = value;
      } else {
        this.pageInfo2.curPage = value;
      }
      this.refreshData();
    },
    handleClick(tab) {
      this.curIndex = tab.index;
      this.refreshData();
    },
    formatType(row, column) {
      if (row.type == 0) {
        return "幻灯片直播";
      } else if (row.type == 1) {
        return "视频录播";
      } else if (row.type == 2) {
        return "视频直播";
      }
      return "未知";
    },
    formatTag(row, column) {
      for (let i = 0; i < this.tagList.length; i++) {
        var item = this.tagList[i];
        if (row.tag == item.value) {
          return item.label;
        }
      }
      return "未知";
    },
    formatStatus: function(row, column) {
      if (row.status == 0) {
        return "即将开始";
      } else if (row.status == 1) {
        return "直播中";
      } else if (row.status == 2) {
        return "直播结束";
      }
      return "未知";
    },
    formatDatastatus: function(row, column) {
      if (row.datastatus == 0) {
        return "已删除";
      } else if (row.datastatus == 1) {
        return "已上架";
      } else if (row.datastatus == 2) {
        return "已下架";
      } else if (row.datastatus == -1) {
        return "已隐藏";
      }
      return "未知";
    },
    // 获取课程列表
    refreshData() {
      var _this = this;
      var endtime = "";
      if (this.endtime) {
        var endDate = new Date(this.endtime);
        var endDate_s = endDate.getTime(); // 转化为时间戳毫秒数
        endDate.setTime(
          endDate_s + 1000 * 59 + 1000 * 60 * 59 + 1000 * 60 * 60 * 23
        );
        endtime = _this.FormatDate(endDate, "yyyy-MM-dd hh:mm:ss");
      }

      const para = {
        funcid: _this.curIndex == 0 ? "1003" : "1004",
        anchorid: this.room.anchorid,
        condition: this.condition,
        starttime: this.starttime ? this.starttime : "",
        endtime: endtime,
        datastatus: this.courseState,
        page: this.showPage,
        pagesize: this.showPageSize
      };
      if (_this.curIndex == 0) {
        para.tag = this.tag;
      }
      _this.listLoading = true;
      _this.getData(para).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          // 单课程
          if (_this.curIndex == 0) {
            _this.pageInfo1.curPage = res.data.page;
            _this.pageInfo1.pageSize = res.data.pagesize;
            _this.pageInfo1.total = res.data.totalcount;

            // 系列课
          } else {
            _this.pageInfo2.curPage = res.data.page;
            _this.pageInfo2.pageSize = res.data.pagesize;
            _this.pageInfo2.total = res.data.totalcount;
          }

          _this.data_list = res.data.records;
        }
        _this.listLoading = false;
      });
    },
    // 删除
    handleDel: function(index, row) {
      var _this = this;
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      }).then(() => {
        _this.listLoading = true;
        _this.edtCourse({
          signleid: row.signleid,
          seriesid: row.seriesid,
          datastatus: "0"
        });
      });
    },
    // 显示编辑界面
    handleEdit: function(index, row, operation) {
      var _this = this;
      if (this.curIndex == 0) {
        if (operation == "push") {
          _this.pushDialogVisible = true;
          _this.requestPushUrl(row);
        } else if (operation == "edit") {
          _this.$router.push({
            name: "单课程",
            query: { title: row.title, signleid: row.signleid, type: row.type },
            params: _.cloneDeep(row)
          });
        } else if (operation == "status") {
          _this.listLoading = true;
          if (row.datastatus == 1) {
            _this.edtCourse({
              signleid: row.signleid,
              datastatus: "2"
            });
          } else if (row.datastatus == 2) {
            _this.edtCourse({
              signleid: row.signleid,
              datastatus: "1"
            });
          } else {
            _this.listLoading = false;
          }
        }
      } else {
        if (operation == "manager") {
          this.$router.push({
            path: "/courselist/singlelistofseries",
            query: { title: row.title, seriesid: row.seriesid }
          });
        } else if (operation == "edit") {
          this.$router.push({
            path: "/courselist/seriescourse",
            query: { title: row.title, seriesid: row.seriesid }
          });
        } else if (operation == "status") {
          _this.listLoading = true;
          if (row.datastatus == 1) {
            _this.edtCourse({
              seriesid: row.seriesid,
              datastatus: "2"
            });
          } else if (row.datastatus == 2) {
            _this.edtCourse({
              seriesid: row.seriesid,
              datastatus: "1"
            });
          }
        }
      }
    },
    edtCourse(alterData) {
      var _this = this;
      var param = alterData;
      if (alterData.signleid) {
        param.funcid = "1021";
      } else {
        param.funcid = "1024";
      }
      param.anchorid = _this.room.anchorid;
      _this.listLoading = true;
      _this.getData(param).then(res => {
        if (res.code == 1) {
          _this.refreshData();
        }
        _this.listLoading = false;
      });
    },
    // 显示新增界面
    handleAdd: function() {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        sex: -1,
        age: 0,
        birth: "",
        addr: ""
      };
    },

    // 新增
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("确认提交吗？", "提示", {}).then(() => {
            this.addLoading = true;
            // NProgress.start();
            const para = Object.assign({}, this.addForm);
            para.birth =
              !para.birth || para.birth == ""
                ? ""
                : util.formatDate.format(new Date(para.birth), "yyyy-MM-dd");
            addUser(para).then(res => {
              this.addLoading = false;
              // NProgress.done();
              this.$message({
                message: "提交成功",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.refreshData();
            });
          });
        }
      });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    // 批量删除
    batchRemove: function() {
      var ids = this.sels.map(item => item.id).toString();
      this.$confirm("确认删除选中记录吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          // NProgress.start();
          const para = { ids: ids };
          batchRemoveUser(para).then(res => {
            this.listLoading = false;
            // NProgress.done();
            this.$message({
              message: "删除成功",
              type: "success"
            });
            this.refreshData();
          });
        })
        .catch(() => {});
    },
    getCourseTagList() {
      var _this = this;
      _this.getData({ funcid: 1029 }).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          var records = res.data.records;
          for (let i = 0; i < records.length; i++) {
            var item = records[i];
            _this.tagList.push({ value: item.tagid, label: item.tagname });
          }
          _this.tagList.unshift({ value: "", label: "全部" });
        }
        _this.listLoading = false;
      });
    },
    clickItem(item) {
      this.$router.push({
        name: "单课程",
        query: { title: "新增单课程", type: item.type, signleid: "" }
      });
      this.dialogFormVisible = false;
    }
  },
  watch: {
    curIndex(val) {
      var _this = this;
      _this.$nextTick(() => {
        _this.$root.topbarbtntitle = _this.bartitle;
      });
    }
  },
  computed: {
    bartitle() {
      return this.curIndex == 0 ? "新增单课程" : "新增系列课";
    },
    showPage: {
      get() {
        if (this.curIndex == 0) {
          return this.pageInfo1.curPage;
        } else {
          return this.pageInfo2.curPage;
        }
      },
      // setter 方法
      set(newValue) {
        if (this.curIndex == 0) {
          this.pageInfo1.curPage = newValue;
          return this.pageInfo1.curPage;
        } else {
          this.pageInfo2.curPage = newValue;
          return this.pageInfo2.curPage;
        }
      }
    },
    showPageSize() {
      if (this.curIndex == 0) {
        return this.pageInfo1.pageSize;
      } else {
        return this.pageInfo2.pageSize;
      }
    },
    showTotal() {
      if (this.curIndex == 0) {
        return this.pageInfo1.total;
      } else {
        return this.pageInfo2.total;
      }
    },
    showTotalPage() {
      if (this.curIndex == 0) {
        return Math.ceil(this.pageInfo1.total / this.pageInfo1.pageSize);
      } else {
        return Math.ceil(this.pageInfo2.total / this.pageInfo2.pageSize);
      }
    }
  },
  beforeMount() {},
  activated() {
    var _this = this;

    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = _this.bartitle;
    });
    // 点击右上角按钮
    _this.GlobalEvent.$on("clickRightBtn", () => {
      if (_this.curIndex == 0) {
        _this.dialogFormVisible = true;
        // _this.$router.push({ path: '/courselist/singlecourse', query: { signleid: '', title: '新增单课程' }, params: { p: 1 } })
      } else {
        _this.$router.push({
          path: "/courselist/seriescourse",
          query: { seriesid: "", title: "新增系列课" }
        });
      }
    });
    _this.refreshData();
  },
  deactivated() {
    this.GlobalEvent.$off("clickRightBtn");
  },
  mounted() {
    var _this = this;
    this.$nextTick(() => {
      _this.getCourseTagList();
      var user = sessionStorage.getItem("user");
      var room = sessionStorage.getItem("room");
      if (user) {
        user = JSON.parse(user);
        _this.sysUserName = user.docname || "";
        _this.sysUserAvatar = user.picture || "";
        _this.user = user;
      }
      if (room) {
        room = JSON.parse(room);
        _this.room = room;
      }
      // _this.refreshData();
    });
  },
  beforeDestroy() {}
};
</script>

