<template>
    <div class="limit-input">
        <el-input v-model="val" @input.native="handleInput" @change="handleChange"></el-input>
    </div>
</template>
<script>
export default {
    props:['value','pattern'],
    data(){
        return {
            val:'',
            reg:null
        }
    },
    mounted() {
        this.val = this.value;
        if(this.pattern){
            this.reg = new RegExp(this.pattern);
        }
    },
    methods:{
        handleChange(val){
            this.$emit('input',val)
            this.$emit('change',val)
        },
        handleInput(){
            this.$emit('input',this.val)
        }
    },
    watch:{
        value(){
            this.val = this.value;
        },
        val(newVal,oldVal){
            if(this.reg&&!this.reg.test(newVal)){
                this.$nextTick(()=>{
                    this.val = oldVal
                    this.$emit('input',this.val)
                })
            }
        }
    }
}
</script>




