<template>
  <div class="app-container">
    <el-form ref="form" :rules="rules" :model="form" label-width="120px">
      <el-form-item label="Banner名称" prop="bannername">
        <el-input placeholder="请输入名称,最多20个字" v-model="form.bannername" />
      </el-form-item>
      <el-form-item label="Banner图片" prop="url">
        <div class="head_icon">
          <img width="auto" height="120px" :src="form.url" :onerror="errorImg" />
        </div>
        <el-upload
          class="upload-demo"
          accept="image/png, image/jpg"
          action
          :auto-upload="false"
          :show-file-list="false"
          :limit="1"
          :file-list="fileList"
          :on-change="changeUpload"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">支持上传jpg/png文件,图片尺寸:1280x560px,且不超过5M</div>
        </el-upload>
      </el-form-item>

      <el-form-item label="选择类型" prop="type">
        <el-radio-group v-model="form.type">
          <el-radio :label="0">外链地址</el-radio>
          <el-radio :label="1">链接课程</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item v-if="form.type == 0" label="跳转地址" prop="detailurl">
        <el-input v-model="form.detailurl" placeholder="请输入链接地址" />
      </el-form-item>

      <el-form-item v-if="form.type == 1" label="跳转课程" prop="coursename">
        <span>{{form.coursename}}</span>
        <el-button @click="chooseCourse">选择课程</el-button>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit" :loading="saveLoading">保存</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </el-form-item>
    </el-form>

    <choose-course :show="courseListVisible" @visible="visibleChange" @commit="getCourse"></choose-course>

    <!-- vueCropper 剪裁图片实现-->

    <el-dialog title="图片剪裁" :visible.sync="dialogVisible" append-to-body>
      <div class="cropper-content">
        <div class="cropper" style="text-align:center">
          <!-- @realTime="realTime" -->

          <vueCropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.size"
            :outputType="option.outputType"
            :info="true"
            :full="option.full"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :autoCrop="option.autoCrop"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :centerBox="option.centerBox"
            :infoTrue="option.infoTrue"
            :fixedBox="option.fixedBox"
          ></vueCropper>
        </div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false;loading=false">取 消</el-button>
        <el-button type="primary" @click="finish(option.fixedNumber)" :loading="loading">确认</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ChooseCourse from "@/components/Course/chooseCourse";
import limitInput from "@/components/limitInput";
export default {
  components: {
    ChooseCourse,
    limitInput
  },
  watch: {
    "form.bannername": function(val, oldval) {
      if (val.length > 20) {
        this.form.bannername = oldval;
      }
    }
  },
  data() {
    return {
      bannerid: "",
      fileinfo: "",
      loading: false,
      dialogVisible: false,
      // 裁剪组件的基础配置option
      option: {
        img: "", // 裁剪图片的地址
        info: true, // 裁剪框的大小信息
        outputSize: 0.8, // 裁剪生成图片的质量
        outputType: "jpeg", // 裁剪生成图片的格式
        canScale: false, // 图片是否允许滚轮缩放
        autoCrop: true, // 是否默认生成截图框
        // autoCropWidth: 320, // 默认生成截图框宽度
        // autoCropHeight: 180, // 默认生成截图框高度
        fixedBox: false, // 固定截图框大小 不允许改变
        fixed: true, // 是否开启截图框宽高固定比例
        fixedNumber: [1280, 560], // 截图框的宽高比例
        full: true, // 是否输出原图比例的截图
        canMoveBox: true, // 截图框能否拖动
        original: false, // 上传图片按照原始比例渲染
        centerBox: true, // 截图框是否被限制在图片里面
        infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
      },
      courseListVisible: false,
      uploading: false,
      saveLoading: false,
      errorImg: 'this.src="' + require("@/assets/img/default_nodata.png") + '"',
      fileList: [],
      form: {
        bannername: "",
        url: "",
        detailurl: "",
        courseid: "",
        coursename: "",
        coursetype: "",
        type: ""
      },
      rules: {
        bannername: [
          { required: true, message: "请输入Banner名称", trigger: "change" }
        ],
        url: [
          { required: true, message: "请选择Banner图片", trigger: "change" }
        ],
        type: [{ required: true, message: "请选择类型", trigger: "change" }],
        detailurl: [
          { required: true, message: "请输入跳转链接", trigger: "change" }
        ],
        coursename: [
          { required: true, message: "请选择课程", trigger: "change" }
        ]
      }
    };
  },

  methods: {
    getCourse(item) {
      this.form.coursename = item.title;
      if (item.signleid && item.signleid != "") {
        this.form.coursetype = 0;
        this.form.courseid = item.signleid;
      } else {
        this.form.coursetype = 1;
        this.form.courseid = item.seriesid;
      }
    },
    visibleChange(value) {
      this.courseListVisible = value;
    },

    // 上传按钮   限制图片大小
    changeUpload(file, fileList) {
      var _this = this;
      this.option.fixedNumber = [1280, 560];
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isLt5M) {
        this.$message.error("上传文件大小不能超过 5MB!");
        this.fileList = [];
        return false;
      }
      var filetype = file.raw.type.toLowerCase();
      if (
        filetype.indexOf("jpg") == -1 &&
        filetype.indexOf("jpeg") == -1 &&
        filetype.indexOf("png") == -1
      ) {
        this.$message.error("图片类型错误");
        this.fileList = [];
        return false;
      }
      this.fileinfo = file;
      const imgurl = URL.createObjectURL(file.raw);
      // const imgurl =
      //   "blob:http://10.1.1.51:8080/ffb27d60-ab75-4443-8847-90e07f56122c";
      // 上传成功后将图片地址赋值给裁剪框显示图片
      this.$nextTick(() => {
        _this.option.img = imgurl;
        _this.dialogVisible = true;
      });
      _this.fileList = [];
    },

    // 点击裁剪，这一步是可以拿到处理后的地址
    finish(ratio) {
      var _this = this;
      this.$refs.cropper.getCropBlob(data => {
        var fileName = "goods" + _this.fileinfo.uid;
        this.loading = true;
        // 上传阿里云服务器
        setTimeout(() => {
          // var file = _this.convertBase64UrlToBlob(data)
          const imgurl = URL.createObjectURL(data);
          const param = new FormData(); // 创建form对象
          param.append("file", data, _this.fileinfo.name); // 通过append向form对象添加数据
          param.append("filetype", "jpeg");

          _this.uploadImage(param).then(res => {
            if (res.code == 1) {
              console.log(res.list.url);
              _this.form.url = res.list.url;
            }
            _this.loading = false;
            _this.dialogVisible = false;
            // _this.$refs.form.validateField("url");
          });
        }, 0);
      });
    },
    cancelAction() {
      this.$router.go(-1);
    },
    onSubmit() {
      var _this = this;
      var form = _this.form;
      this.$refs.form.validate(valid => {
        if (valid) {
          console.log(valid);
          var param = this.form;
          if (this.form.type == 0) {
            param.courseid = "";
            param.coursename = "";
            param.coursetype = "";
          } else if (this.form.type == 1) {
            param.detailurl = "";
          }
          param.funcid = 1105;
          _this.getData(param).then(res => {
            if (res.code == 1) {
              console.log(res);
              _this.$message({
                message: "保存成功",
                type: "success"
              });
              _this.$router.go(-1);
            }
          });
        }
      });
    },
    onCancel() {
      this.$message({
        message: "cancel!",
        type: "warning"
      });
    },
    chooseCourse() {
      this.courseListVisible = true;
    },
    requestData() {
      var _this = this;
      _this.getData({ funcid: 1104, bannerid: _this.bannerid }).then(res => {
        if (res.code == 1) {
          var data = res.data;
          _this.form = Object.assign(_this.form, data);
          _this.form.type = parseInt(_this.form.type);
        }
      });
    }
  },
  mounted() {
    // var obj2 = Object.assign({}, obj);
    var _this = this;
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = "";
    });
    this.bannerid = this.$route.query.bannerid;
    if (this.bannerid) {
      this.requestData();
    }
  }
};
</script>

<style lang="scss" scoped>
.gap {
  width: 30px;
}

.line {
  text-align: center;
}

.banner_pic {
  width: 320px;

  height: 240px;
}

/deep/ .el-input {
  width: 240px;
}

/deep/ .el-textarea {
  width: 360px;
}

.cropper-content {
  .cropper {
    width: auto;

    height: 300px;
  }
}
</style>



