<template>
  <section>
    <!--列表-->
    <el-table
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="mobile" label="账号" width="120"></el-table-column>
      <el-table-column prop="role" label="角色" width="120" :formatter="formatRole"></el-table-column>
      <el-table-column prop="name" label="姓名" width="100"></el-table-column>
      <el-table-column prop="lastlogintime" label="上次登录时间"></el-table-column>

      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <!-- <el-button type="text"
                     size="small"
                     @click="handleEdit(scope.$index, scope.row)">停用</el-button>
          <span>|</span>-->
          <el-button type="text" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        background
        layout="sizes,prev,pager,next,slot"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="pageInfo1.curPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pageInfo1.pageSize"
        :total="pageInfo1.total"
        style="float:right;"
      >
        <span>&nbsp;共{{Math.ceil(pageInfo1.total / pageInfo1.pageSize)}}页</span>
      </el-pagination>
    </el-col>

    <el-dialog top="200px" title="添加管理员" :visible.sync="dialogFormVisible">
      <div class="user_wraper">
        <div v-if="addStatus== 0">
          <el-input v-model="phone" autocomplete="off" style="width:280px;margin-right:20px;"></el-input>
          <el-button type="primary" @click="searchPhone" :loading="searchLoading">查询</el-button>
        </div>
        <div v-if="addStatus== 1">
          <div class="flex_c bottom_line">
            <img :src="user.picture" width="50px" height="50px" style="border-radius:25px;">
            <span
              class="flex_c"
              style="flex-direction: column;margin-left:8px;text-align: left;align-items: flex-start;"
            >
              <span>{{user.mobile}}</span>
              <span>{{user.name}}</span>
            </span>
          </div>
          <div style="margin-top:10px;text-align: left;">
            <span style="color:#333333">确定要添加其为管理员吗?</span>
          </div>
        </div>
        <div v-if="addStatus== 2">
          <span class="fc-ff4545">该手机号未注册牙医管家,请注册后再添加管理员</span>
        </div>
      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmAdd" :loading="confrimLoading">确 定</el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      room: {},
      data_list: [],
      pageInfo1: {
        pageSize: 10,
        curPage: 1,
        total: 1
      },
      addStatus: 0, // 查询状态 0 闲置 1 成功 2 未查询到牙医管家账号
      listLoading: false,
      searchLoading: false,
      confrimLoading: false,
      phone: "",
      user: null,
      dialogFormVisible: false,
      managerform: {
        phone: ""
      },
      dialogLoading: false
    };
  },
  methods: {
    formatRole(rowData) {
      if (rowData.belongid == rowData.userid) {
        return "超级管理员";
      } else {
        return "管理员";
      }
    },
    resetsearchData() {},
    requestListData() {
      var _this = this;
      const para = {
        funcid: "1014",
        anchorid: this.room.anchorid,
        page: this.pageInfo1.curPage,
        pagesize: this.pageInfo1.pageSize
      };
      _this.listLoading = true;
      _this.getData(para).then(res => {
        if (res.code == 1) {
          if (res.data.records && res.data.records.length > 0) {
            _this.data_list = res.data.records;
          } else {
            _this.data_list = [];
          }
        }
        _this.listLoading = false;
      });
    },
    addManagerRequest(callback) {
      var _this = this;
      const para = {
        funcid: "1015",
        anchorid: this.room.anchorid,
        belongid: this.user.userid
      };
      _this.confrimLoading = true;
      _this.getData(para).then(res => {
        if (res.code == 1) {
          _this.$message({
            message: "添加成功",
            type: "success"
          });
        }
        _this.confrimLoading = false;
        if (callback) {
          callback();
        }
      });
    },
    delManagerRequest(data) {
      var _this = this;
      const para = {
        funcid: "1016",
        anchorid: data.userid,
        belongid: data.belongid
      };
      _this.listLoading = true;
      _this.getData(para).then(res => {
        if (res.code == 1) {
          _this.$message({
            message: "删除成功",
            type: "success"
          });
        }
        _this.requestListData();
        _this.listLoading = false;
      });
    },
    confirmAdd() {
      var _this = this;
      if (_this.addStatus == 0) {
        _this.dialogFormVisible = false;
      } else if (_this.addStatus == 1) {
        _this.addManagerRequest(() => {
          _this.requestListData();
          _this.addStatus == 0;
          _this.dialogFormVisible = false;
        });
      }
    },
    searchPhone() {
      var _this = this;
      _this.addStatus = 0;
      if (!this.phone) {
        this.$message.error("请输入手机号");
        return;
      }
      const para = {
        funcid: "1017",
        anchorid: this.room.anchorid,
        mobile: this.phone
      };
      _this.searchLoading = true;
      _this.getData(para).then(res => {
        if (res.code == 1) {
          if (res.data) {
            _this.user = res.data;
            _this.addStatus = 1;
          } else {
            _this.user = null;
            _this.addStatus = 2;
          }
        }
        _this.searchLoading = false;
      });
    },
    handleSizeChange(value) {
      this.pageInfo1.curPage = 1;
      this.pageInfo1.pageSize = value;
      this.requestListData();
    },
    handleCurrentChange(value) {
      this.pageInfo1.curPage = value;
      this.requestListData();
    },
    // 删除
    handleDel: function(index, row) {
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          // NProgress.start();
          this.delManagerRequest(row);
        })
        .catch(() => {});
    },
    // 显示编辑界面
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },

    selsChange: function(sels) {
      this.sels = sels;
    },
    // 批量删除
    batchRemove: function() {
      var ids = this.sels.map(item => item.id).toString();
      this.$confirm("确认删除选中记录吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          // NProgress.start();
          const para = { ids: ids };
          batchRemoveUser(para).then(res => {
            this.listLoading = false;
            // NProgress.done();
            this.$message({
              message: "删除成功",
              type: "success"
            });
            this.getUsers();
          });
        })
        .catch(() => {});
    }
  },
  watch: {
    total(newValue, oldValue) {
      this.toalPage = Math.ceil(newValue / this.pageSize);
    },
    pageSize(newValue, oldValue) {
      this.toalPage = Math.ceil(this.total / newValue);
    },
    dialogFormVisible(val) {
      if (val) {
        this.resetsearchData();
      }
    }
  },
  mounted() {
    var _this = this;

    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = "新增管理员";
      // 点击右上角按钮
      _this.GlobalEvent.$on("clickRightBtn", () => {
        _this.addStatus = 0;
        _this.dialogFormVisible = true;
      });

      var room = sessionStorage.getItem("room");
      if (room) {
        room = JSON.parse(room);
        _this.room = room;
      }
      _this.requestListData();
    });
  },
  beforeDestroy() {
    this.GlobalEvent.$off("clickRightBtn");
  }
};
</script>

<style scoped>
.toolbar {
  padding: 10px;
}
.fc-ff4545 {
  color: #ff4545;
}
.user_wraper {
  padding-left: 20px;
  padding-right: 20px;
}
.bottom_line {
  border-bottom: 1px solid #f0f0f0;
  padding-bottom: 10px;
}
.flex_c {
  display: flex;
  align-items: center;
  /* justify-content: center; */
}
</style>


