// import axios from 'axios';
import request from './request'
import qs from 'qs'
// process.env.BASE_API
let base
if (process.env.NODE_ENV === 'production') {
  base = ''
} else {
  base = '/api'
}

export const LoginTabIndex = 'vue_admin_template_loginIndex' // 当前tabbar索引
export const LoginKey = 'vue_admin_template_loginInfo' // 输入用户信息
export const UserKey = 'vue_admin_template_user' // 登录后用户信息

export const getData = params => {
  const finalParam = {
    param: {
      params: params
    }
  }
  return request
    .get(`${base}/course/func`, {
      params: finalParam
    })
    .then(res => (res.data ? res.data:{}))
}

export const postData = params => {
  const finalParam = {
    param: {
      params: params
    }
  }
  // qs.stringify()
  return request.post(`${base}/course/func`, finalParam).then(res => (res.data ? res.data : {}))
}

export const uploadImage = params => {
  const config = {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  } // 添加请求头
  return request
    .post(`${base}/course/uploadimage`, params, config)
    .then(res => res.data)
}

export const baseUrl = `${base}`



