import Cookies from 'js-cookie'

const SessionKey = 'vue_admin_template_session'
const UseridKey = 'vue_admin_template_userid'
const AnchoridKey = 'vue_admin_template_Anchorid'

export function getSession() {
  return Cookies.get(SessionKey)
}

export function setSession(session) {
  return Cookies.set(SessionKey, session)
}

export function removeSession() {
  return Cookies.remove(SessionKey)
}

export function getUserid() {
  return Cookies.get(UseridKey)
}

export function setUserid(userid) {
  return Cookies.set(UseridKey, userid)
}

export function removeUserid() {
  return Cookies.remove(UseridKey)
}


export function getAnchorid() {
  return Cookies.get(AnchoridKey)
}

export function setAnchorid(Anchorid) {
  return Cookies.set(AnchoridKey, Anchorid)
}

export function removeAnchorid() {
  return Cookies.remove(AnchoridKey)
}


