<template>
  <section v-if="$route.path != rootPath">
    <router-view></router-view>
  </section>
  <section v-else>
    <!--工具条-->
    <div class="topbar">
      <div style="float:left;margin-top:4px;">
        <el-button type="primary" size="small" @click.native="toolAction(0)">添加单课程</el-button>
        <el-button type="primary" size="small" @click.native="toolAction(1)">上架所有单课程</el-button>
        <el-button type="primary" size="small" @click.native="toolAction(2)">下架所有单课程</el-button>
      </div>

      <div style="float:right;font-size:14px;" class="date_area">
        <!-- 开播时间
        <el-date-picker style="width:150px"
                        v-model="begintime"
                        type="date"
                        placeholder="选择日期时间">
        </el-date-picker>
        至
        <el-date-picker style="width:150px"
                        v-model="autoendtime"
                        type="date"
                        placeholder="选择日期时间">
        </el-date-picker>-->
        <span>&nbsp;上架状态</span>
        <el-select style="width:90px;" v-model="courseStatus" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
        <el-input style="width:180px" autosize placeholder="请输入内容" v-model="condition"></el-input>
        <el-button type="primary" size="small" v-on:click="requestSinglelist">查询</el-button>
      </div>
    </div>
    <!--列表-->
    <el-table
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="banner" label="封面" width="120">
        <template slot-scope="scope">
          <!-- // 9 / 16  -->
          <img
            style="margin-top:8px;min-height: 54px;"
            :src="scope.row.banner"
            height="54px;"
            width="96px;"
            alt
          />
        </template>
      </el-table-column>
      <el-table-column prop="title" label="课程名称" width="220"></el-table-column>
      <el-table-column prop="tag" label="课程分类" width="90" :formatter="formatTag"></el-table-column>
      <el-table-column prop="type" label="课程类型" width="120" :formatter="formatType"></el-table-column>
      <el-table-column prop="status" label="直播状态" width="100" :formatter="formatStatus"></el-table-column>
      <!-- <el-table-column prop="anchorname"
                       label="主播"
                       width="120">
      </el-table-column>-->
      <el-table-column prop="fee" label="原价" min-width="80"></el-table-column>
      <el-table-column prop="soldfee" label="销售价" min-width="80"></el-table-column>
      <!-- <el-table-column prop="buynum"
                       label="参加人数"
                       min-width="70">
      </el-table-column>
      <el-table-column prop="begintime"
                       label="开播时间"
                       min-width="100"
                       sortable>
      </el-table-column>
      <el-table-column prop="autoendtime"
                       label="结束时间"
                       min-width="100"
                       sortable>
      </el-table-column>-->
      <!-- :formatter="formatDatastatus" -->
      <el-table-column prop="datastatus" label="上架状态" min-width="70">
        <template slot-scope="scope">
          <span
            :class="[scope.row.datastatus == 2 ? 'fc-ff3a3a':'']"
          >{{formatDatastatus(scope.row,scope.column)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <!-- <el-button type="text"
                     size="small"
                     @click="handleEdit(scope.$index, scope.row,'edit')">修改</el-button>
          <span>|</span>-->
          <el-button
            type="text"
            size="small"
            @click="handleEdit(scope.$index, scope.row,'status')"
          >{{scope.row.datastatus == 1?'下架':'上架'}}</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        background
        layout="sizes,prev,pager,next,slot"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="curPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pageSize"
        :total="toal"
        style="float:right;"
      >
        <span>&nbsp;共{{Math.ceil(toal/pageSize)}}页</span>
      </el-pagination>
    </el-col>
    <!--编辑界面-->
    <el-dialog title="选择课程" :visible.sync="singleListVisible" :close-on-click-modal="false">
      <!--列表-->
      <el-table
        class="dialog_body"
        :data="chooseCourse.data_list"
        stripe
        border
        highlight-current-row
        v-loading="listLoading"
        @selection-change="selsChange"
        style="width: 100%;"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="banner" label="封面" width="120">
          <template slot-scope="scope">
            <!-- // 9 / 16  -->
            <img
              style="margin-top:8px;min-height: 54px"
              :src="scope.row.banner"
              height="54px;"
              width="96px;"
              alt
            />
          </template>
        </el-table-column>
        <el-table-column prop="title" label="课程名"></el-table-column>
        <!-- <el-table-column prop="anchorname"
                         label="主播"
                         width="120">
        </el-table-column>-->
        <!-- <el-table-column prop="age"
                         label="点击人数"
                         width="120"
                         sortable>
        </el-table-column>
        <el-table-column prop="birth"
                         label="购买人数"
                         width="120"
                         sortable>
        </el-table-column>-->
        <el-table-column prop="status" label="课程状态" width="120" :formatter="formatStatus"></el-table-column>
        <el-table-column prop="fee" label="价格" width="120"></el-table-column>
      </el-table>
      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-pagination
          background
          layout="sizes,prev,pager,next,slot"
          @size-change="handleSizeChange1"
          @current-change="handleCurrentChange1"
          :current-page.sync="chooseCourse.curPage"
          :page-sizes="[10, 20, 30, 50]"
          :page-size="chooseCourse.pageSize"
          :total="chooseCourse.total"
          style="float:right;"
        >
          <span>&nbsp;共{{Math.ceil(chooseCourse.total/chooseCourse.pageSize)}}页</span>
        </el-pagination>
      </el-col>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="singleListVisible = false">取消</el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="addLoading">确定</el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import Vue from "vue";
// import NProgress from 'nprogress'
import { getUserListPage, removeUser, editUser } from "@/utils/networks/api";

export default {
  data() {
    return {
      rootPath: "/courselist/singlelistofseries",
      tagList: [],
      seriesInfo: {}, // 系列课详情
      chooseCourse: {
        listLoading: false,
        data_list: [],
        total: 0,
        pageSize: 10
      },
      sels: [], // 列表选中列
      addLoading: false,
      singleListVisible: false,
      listLoading: false,
      begintime: "",
      autoendtime: "",
      condition: "",
      courseStatus: "",
      pageSize: 10,
      curPage: 1,
      toal: 1,
      data_list: [],
      options: [
        // {
        //   value: '0',
        //   label: '已删除'
        // },
        {
          value: "1",
          label: "已上架"
        },
        {
          value: "2",
          label: "已下架"
        }
        // ,
        // {
        //   value: '-1',
        //   label: '已隐藏'
        // }
      ]
    };
  },
  watch: {
    // 如果 `question` 发生改变，这个函数就会运行
    question(newQuestion, oldQuestion) {
      // this.answer = 'Waiting for you to stop typing...'
    }
  },
  computed: {
    // 计算属性的 getter
    reversedMessage() {
      return "";
    }
  },
  methods: {
    getCourseTagList(callback) {
      var _this = this;
      _this.getData({ funcid: 1029 }).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          var records = res.data.records;
          for (let i = 0; i < records.length; i++) {
            var item = records[i];
            _this.tagList.push({ value: item.tagid, label: item.tagname });
          }
          _this.tagList.unshift({ value: "", label: "全部" });
        }
        _this.listLoading = false;
        if (callback) {
          callback();
        }
      });
    },
    formatTag(row, column) {
      for (let i = 0; i < this.tagList.length; i++) {
        var item = this.tagList[i];
        if (row.tag == item.value) {
          return item.label;
        }
      }
      return "未知";
    },
    // 系列课详情
    requestSeriesDetail() {
      var _this = this;
      _this
        .getData({
          funcid: "1007",
          seriesid: _this.seriesInfo.seriesid
        })
        .then(res => {
          if (res.code == 1) {
            _this.seriesInfo = res.data.records[0];
          }
        });
    },
    // 选中框中单课列表
    requestAddSinglelist() {
      var _this = this;
      _this.chooseCourse.listLoading = true;
      _this
        .getData({
          funcid: "1027",
          seriesid: _this.seriesInfo.seriesid,
          page: _this.chooseCourse.curPage,
          pagesize: _this.chooseCourse.pageSize
        })
        .then(res => {
          if (res.code == 1) {
            _this.chooseCourse.data_list = res.data.records;
            _this.chooseCourse.curPage = res.data.page;
            _this.chooseCourse.pageSize = res.data.pagesize;
            _this.chooseCourse.total = res.data.totalcount;
          }
          _this.chooseCourse.listLoading = false;
        });
    },
    // 系列课下单课列表
    requestSinglelist() {
      var _this = this;
      _this.listLoading = true;
      _this
        .getData({
          funcid: "1012",
          seriesid: _this.seriesInfo.seriesid,
          begintime: _this.begintime,
          autoendtime: _this.autoendtime,
          condition: _this.condition,
          datastatus: _this.courseStatus
        })
        .then(res => {
          if (res.code == 1) {
            _this.data_list = res.data.records;
            _this.curPage = res.data.page;
            _this.pageSize = res.data.pagesize;
            _this.total = res.data.totalcount;
          }
          _this.listLoading = false;
        });
    },
    // 新增单课到系列课
    addSingleList() {
      var _this = this;
      var signlelist = [];
      var addfee = 0;
      var discount = _this.seriesInfo.discount / 100;
      _.forEach(_this.sels, function(value, index) {
        signlelist.push({
          signleid: value.signleid,
          fee: value.fee,
          soldfee: value.fee * discount
        });
        addfee += parseFloat(value.fee * discount);
      });
      _this.addLoading = true;
      _this
        .getData({
          funcid: "1009",
          seriesid: _this.seriesInfo.seriesid,
          addfee: addfee,
          signlelist: signlelist
        })
        .then(res => {
          if (res.code == 1) {
            console.log(res);
            _this.requestSinglelist();
          }
          _this.singleListVisible = false;
          _this.addLoading = false;
        })
        .catch(error => {
          _this.addLoading = false;
        });
    },
    // 删除指定单课
    delSingleOfList(item) {
      var _this = this;
      _this
        .getData({
          funcid: "1010",
          seriesid: _this.seriesInfo.seriesid,
          childid: item.childid
        })
        .then(res => {
          if (res.code == 1) {
            _this.requestSinglelist();
          }
          _this.listLoading = false;
        });
    },
    uploadAllCourse(condition) {
      var _this = this;
      _this
        .getData({
          funcid: "1026",
          seriesid: _this.seriesInfo.seriesid,
          condition: condition
        })
        .then(res => {
          if ((res.code = 1)) {
            _this.requestSinglelist();
          }
        });
    },
    edtCourse(alterData) {
      var _this = this;
      var param = alterData;
      param.funcid = "1021";
      _this.listLoading = true;
      _this.getData(param).then(res => {
        if (res.code == 1) {
          _this.requestSinglelist();
        }
        _this.listLoading = false;
      });
    },
    formatType: function(row, column) {
      if (row.type == 0) {
        return "PPT直播";
      } else if (row.type == 1) {
        return "课程录播";
      } else if (row.type == 2) {
        return "课程直播";
      }
      return "未知";
    },
    formatStatus: function(row, column) {
      if (row.status == 0) {
        return "即将开始";
      } else if (row.status == 1) {
        return "直播中";
      } else if (row.status == 2) {
        return "直播结束";
      }
      return "未知";
    },
    formatDatastatus: function(row, column) {
      if (row.datastatus == 0) {
        return "已删除";
      } else if (row.datastatus == 1) {
        return "已上架";
      } else if (row.datastatus == 2) {
        return "已下架";
      } else if (row.datastatus == -1) {
        return "已隐藏";
      }
      return "未知";
    },
    editLoading() {},
    selsChange(sels) {
      this.sels = sels;
    },
    // 确认添加单课
    editSubmit() {
      this.addSingleList();
    },
    toolAction(index) {
      if (index == 0) {
        this.requestAddSinglelist();
        this.singleListVisible = true;
      } else if (index == 1) {
        this.uploadAllCourse("up");
      } else if (index == 2) {
        this.uploadAllCourse("down");
      }
    },
    handleSizeChange(value) {
      this.pageSize = value;
      this.requestSinglelist();
    },
    handleCurrentChange(value) {
      this.curPage = value;
      this.requestSinglelist();
    },
    handleSizeChange1(value) {
      this.chooseCourse.pageSize = value;
      this.requestAddSinglelist();
    },
    handleCurrentChange1(value) {
      this.chooseCourse.curPage = value;
      this.requestAddSinglelist();
    }, // 显示编辑界面
    handleEdit: function(index, row, operation) {
      var _this = this;
      if (operation == "push") {
        _this.pushDialogVisible = true;
        _this.requestPushUrl(row);
      } else if (operation == "edit") {
        _this.$router.push({
          name: "系列课的单课程",
          query: { title: row.title, signleid: row.signleid, type: row.type },
          params: _.cloneDeep(row)
        });
      } else if (operation == "status") {
        _this.listLoading = true;
        if (row.datastatus == 1) {
          _this.edtCourse({
            signleid: row.signleid,
            datastatus: "2"
          });
        } else if (row.datastatus == 2) {
          _this.edtCourse({
            signleid: row.signleid,
            datastatus: "1"
          });
        }
      }
    },
    // 删除
    handleDel: function(index, row) {
      var _this = this;
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      }).then(() => {
        _this.listLoading = true;
        _this.delSingleOfList(row);
      });
    }
  },
  created() {},
  activated() {
    this.requestSinglelist();
  },
  mounted() {
    var _this = this;
    _this.getCourseTagList(() => {
      _this.$nextTick(() => {
        _this.$root.topbarbtntitle = "";
      });
      _this.seriesInfo.seriesid = _this.$route.query.seriesid;
      _this.requestSeriesDetail();
      _this.requestSinglelist();
    });
  }
};
</script>
>

<style lang="scss" scoped>
.topbar {
  padding-top: 5px;
  padding-bottom: 10px;
  height: 40px;
}
.mywraper {
  color: white;
}
.dialog_body {
  max-height: 320px;
  overflow-y: scroll;
}
.toolbar {
  padding: 10px;
}
.date_area {
  padding-top: 6px;
  /deep/ .el-input__inner {
    height: 30px;
    line-height: 30px;
    border-radius: 2px;
  }
  /deep/ .el-input__icon {
    line-height: 30px;
  }
  /deep/ .el-textarea__inner {
    border-radius: 2px;
  }
}
</style>


