<template>
  <section>
    <div class="topbar">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="收入流水" name="first"></el-tab-pane>
        <el-tab-pane label="提现流水" name="seconds"></el-tab-pane>
      </el-tabs>
    </div>

    <!--列表-->
    <el-table
      v-if="curIndex== 0"
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="orderid" label="交易编号" width="160"></el-table-column>
      <el-table-column prop="buytime" label="交易时间" width="160"></el-table-column>
      <el-table-column prop="title" label="课程名称"></el-table-column>
      <el-table-column prop="paystyle" label="交易类型" width="120"></el-table-column>

      <el-table-column prop="earnings" label="金额" width="160"></el-table-column>
    </el-table>
    <el-table
      v-else-if="curIndex== 1"
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="partner_trade_no" label="交易编号" width="160"></el-table-column>
      <el-table-column prop="payment_time" label="交易时间" width="160"></el-table-column>
      <el-table-column prop="anchorname" label="主播"></el-table-column>
      <el-table-column prop="mobile" label="手机号" width="120"></el-table-column>
      <el-table-column prop="微信" label="交易类型" width="120" :formatter="formatPaytype"></el-table-column>

      <el-table-column prop="amount" label="金额" width="160"></el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        background
        layout="sizes,prev,pager,next,slot"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="curPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pageSize"
        :total="total"
        style="float:right;"
      >
        <span>&nbsp;共{{Math.ceil(total/pageSize)}}页</span>
      </el-pagination>
    </el-col>
  </section>
</template>

<script>
export default {
  data() {
    return {
      activeName: "first",
      classid: "",
      classtype: "",
      curIndex: 0,
      room: "",
      user: "",
      pageSize: 10,
      curPage: 1,
      total: 1,
      data_list: [],
      listLoading: false
    };
  },
  methods: {
    formatPaytype() {
      return "微信";
    },
    handleClick(tab) {
      this.curIndex = tab.index;
      this.curPage = 1;
      this.refrehList();
    },
    refrehList() {
      var _this = this;
      _this.listLoading = true;
      _this
        .getData({
          funcid: _this.curIndex == 0 ? "1019" : "1020",
          page: this.curPage,
          pagesize: this.pageSize,
          classid: this.classid,
          classtype: this.classtype
        })
        .then(res => {
          if (res.code == 1) {
            _this.curPage = res.data.page;
            _this.pageSize = res.data.pagesize;
            _this.data_list = res.data.records;
          }
          _this.listLoading = false;
        });
    },
    handleSizeChange(value) {
      this.pageSize = value;
      this.refrehList();
    },
    handleCurrentChange(value) {
      this.curPage = value;
      this.refrehList();
    }
  },
  watch: {},
  mounted() {
    var _this = this;
    _this.classid = _this.$route.query.classid;
    _this.classtype = _this.$route.query.classtype;
    _this.refrehList();
  }
};
</script>

<style scoped>
.topArea {
  padding-top: 5px;
  padding-bottom: 10px;
}
.toolbar {
  padding: 10px;
}
</style>


