<template>
  <section v-if="$route.path != rootPath">
    <router-view></router-view>
  </section>
  <section v-else>
    <!--列表-->
    <el-table
      :data="data_list"
      stripe
      border
      highlight-current-row
      v-loading="listLoading"
      style="width: 100%;"
    >
      <el-table-column prop="url" label="封面" width="120">
        <template slot-scope="scope">
          <!-- // 9 / 16  -->
          <img
            style="margin-top:8px;min-height:54px;"
            :src="scope.row.url"
            height="54px"
            width="96px"
            alt
          />
        </template>
      </el-table-column>
      <el-table-column prop="coursename" label="课程名称" width="120"></el-table-column>

      <el-table-column prop="coursestatus" label="课程状态" width="90">
        <template slot-scope="scope">
          <span
            :class="[scope.row.coursestatus == 2 ? 'fc-ff3a3a':'']"
          >{{formatCoursestatus(scope.row,scope.column)}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="datastatus" label="上架状态" min-width="70">
        <template slot-scope="scope">
          <span
            :class="[scope.row.datastatus == 2 ? 'fc-ff3a3a':'']"
          >{{formatDatastatus(scope.row,scope.column)}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="160">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            @click="handleEdit(scope.$index, scope.row,'status')"
          >{{scope.row.datastatus == 1?'下架':'上架'}}</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row,'up')">上移</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row,'down')">下移</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row,'edit')">编辑</el-button>
          <span>|</span>
          <el-button type="text" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination
        background
        layout="sizes,prev,pager,next,slot"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="pageInfo.curPage"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pageInfo.pageSize"
        :total="pageInfo.total"
        style="float:right;"
      >
        <span>&nbsp;共{{Math.ceil(this.pageInfo.total / this.pageInfo.pageSize)}}页</span>
      </el-pagination>
    </el-col>
  </section>
</template>

<script>
import Vue from "vue";
// import NProgress from 'nprogress'

export default {
  data() {
    return {
      pushDialogVisible: false,
      dialogFormVisible: false,
      user: {}, // 用户
      rootPath: "/boutiquecourselist",
      data_list: [],
      pageInfo: {
        pageSize: 10,
        curPage: 1,
        total: 1
      },
      listLoading: false
    };
  },
  methods: {
    changeStart() {
      this.pickerOptionsStart = Object.assign({}, this.pickerOptionsStart, {
        disabledDate: time => {
          if (!this.endtime) {
            return;
          }
          return time.getTime() > new Date(this.endtime);
        }
      });
    },
    changeEnd() {
      this.pickerOptionsEnd = Object.assign({}, this.pickerOptionsEnd, {
        disabledDate: time => {
          if (!this.starttime) {
            return;
          }
          return time.getTime() < new Date(this.starttime);
        }
      });
    },
    onCopy() {
      this.$message({
        showClose: true,
        message: "复制成功",
        type: "success"
      });
    },
    handleSizeChange(value) {
      this.pageInfo.curPage = 1;
      this.pageInfo.pageSize = value;
      this.refreshData();
    },
    handleCurrentChange(value) {
      this.pageInfo.curPage = value;
      this.refreshData();
    },
    formatType(row, column) {
      if (row.type == 0) {
        return "链接外链";
      } else if (row.type == 1) {
        return "链接课程";
      }
      return "未知";
    },
    formatStatus: function(row, column) {
      if (row.status == 0) {
        return "即将开始";
      } else if (row.status == 1) {
        return "直播中";
      } else if (row.status == 2) {
        return "直播结束";
      }
      return "未知";
    },
    formatCoursestatus: function(row, column) {
      if (row.coursestatus == 0) {
        return "已删除";
      } else if (row.coursestatus == 1) {
        return "已上架";
      } else if (row.coursestatus == 2) {
        return "已下架";
      } else if (row.coursestatus == -1) {
        return "已隐藏";
      }
      return "未知";
    },
    formatDatastatus: function(row, column) {
      if (row.datastatus == 0) {
        return "已删除";
      } else if (row.datastatus == 1) {
        return "已上架";
      } else if (row.datastatus == 2) {
        return "已下架";
      }
      return "未知";
    },
    // 获取课程列表
    refreshData() {
      var _this = this;
      const para = {
        funcid: "1108"
      };
      _this.listLoading = true;
      _this.getData(para).then(res => {
        _this.listLoading = false;
        if (res.code == 1) {
          _this.pageInfo.curPage = res.data.page;
          _this.pageInfo.pageSize = res.data.pagesize;
          _this.pageInfo.total = res.data.totalcount;
          _this.data_list = res.data.records;
        }
        _this.listLoading = false;
      });
    },
    // 删除
    handleDel: function(index, row) {
      var _this = this;
      var alertData = Object.assign({}, row);
      this.$confirm("确认删除该记录吗?", "提示", {
        type: "warning"
      }).then(() => {
        _this.listLoading = true;
        alertData.datastatus = "0";
        _this.edtBanner(alertData);
      });
    },
    // 显示编辑界面
    handleEdit: function(index, row, operation) {
      var _this = this;
      var alertData = Object.assign({}, row);
      var hotclassid = row.hotclassid;
      if (operation == "edit") {
        _this.$router.push({
          path: "/boutiquecourselist/boutiquedetail",
          query: { title: "编辑热门精品", hotclassid: hotclassid }
        });
      } else if (operation == "status") {
        if (row.datastatus == 1) {
          alertData.datastatus = "2";
          _this.edtBanner(alertData);
        } else if (row.datastatus == 2) {
          alertData.datastatus = "1";
          _this.edtBanner(alertData);
        }
      } else if (operation == "up" || operation == "down") {
        alertData.operation = operation;
        _this.edtBanner(alertData);
      }
    },
    edtBanner(alterData) {
      var _this = this;
      var param = alterData;
      param.funcid = 1110;
      _this.listLoading = true;
      _this.getData(param).then(res => {
        if (res.code == 1) {
          _this.refreshData();
        }
        _this.listLoading = false;
      });
    }
  },
  watch: {},
  computed: {
    bartitle() {
      return "添加课程";
    }
  },
  beforeMount() {},
  activated() {
    var _this = this;
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = _this.bartitle;
    });
    // 点击右上角按钮
    _this.GlobalEvent.$on("clickRightBtn", () => {
      _this.$router.push({
        path: "/boutiquecourselist/BoutiqueDetail",
        query: { title: "添加课程" }
      });
    });
    _this.refreshData();
  },
  deactivated() {
    this.GlobalEvent.$off("clickRightBtn");
  },
  mounted() {},
  beforeDestroy() {}
};
</script>

<style lang='scss' scoped>
.topbar {
  .el-tabs {
    width: 140px;
    float: left;
    display: inline-block;
    //scoped 模式下 深度作用选择器 /deep/
    /deep/ .el-tabs__nav-wrap::after {
      display: none;
    }
  }
}
.toolbar {
  padding: 10px;
}

.date_area {
  padding-top: 8px;
  height: 40px;
  /deep/ .el-input__inner {
    height: 30px;
    line-height: 30px;
    border-radius: 2px;
  }
  /deep/ .el-input__icon {
    line-height: 30px;
  }
}
.el-table th,
/deep/ .el-table td {
  padding: 0px;
}
.class_wraper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.room_item {
  flex: 1;
  display: flex;
  justify-content: center;
  .item_container {
    border: 1px solid #f0f0f0;
    cursor: pointer;
    width: 130px;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    img {
      width: 60px;
      height: 60px;
    }
    .item_title {
      height: 30px;
      line-height: 50px;
    }
  }
}

/deep/ .el-dialog__body {
  padding: 10px 20px;
}
.push_wraper {
  padding: 0;
}
.fc-ff3a3a {
  color: #ff3a3a;
}
</style>


