<template>
  <div class="room_setting">
    <template v-if="$route.path != rootPath">
      <router-view/>
    </template>
    <template v-else>
      <el-form
        ref="form"
        :model="form"
        label-width="120px"
        @submit.prevent="onSubmit"
        style="margin:20px;width:60%;min-width:600px;"
      >
        <el-form-item label="直播间头像">
          <img :src="form.roompicture" class="head_icon" :onerror="errorImg">
          <!-- :http-request="Upload" -->
          <el-upload
            class="upload-demo"
            accept="image/png, image/jpg"
            :file-list="fileList"
            :show-file-list="true"
            :data="uploadData"
            :headers="headers"
            :before-upload="beforeUpload"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            action="http://dtcollege.oss-cn-qingdao.aliyuncs.com/"
          >
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">支持上传jpg/png文件，且不超过5M</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="直播间名称">
          <el-input class="my_input" v-model="form.roomname"></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-col :span="24" class="content">{{form.mobile}}</el-col>
        </el-form-item>
        <el-form-item label="平台抽佣比例">
          <el-col :span="24" class="content">{{form.platformrate}}</el-col>
        </el-form-item>
        <el-form-item label="直播间简介">
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 8}"
            placeholder="请输入内容"
            v-model="form.anchorintro"
          ></el-input>
        </el-form-item>
      </el-form>

      <el-button class="save_btn" type="primary" @click.native="onSubmit" :loading="editLoading">保存</el-button>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      errorImg: 'this.src="' + require('@/assets/img/default_nodata.png') + '"',
      rootPath: '/roomsetting',
      editLoading: false,
      fileList: [],
      fileInfo: {}, // 服务器返回的图片信息
      uploadData: {},
      form: {
        roompicture: '',
        roomname: '1111',
        phone: '13113699720',
        wechatid: 'fghjtdruthykfhkfhj',
        percent: '20%',
        introduce: ''
      }
    }
  },
  methods: {
    beforeUpload(file) {
      var _this = this

      const isLt5M = file.size / 1024 / 1024 < 5
      if (!isLt5M) {
        this.$message.error('上传文件大小不能超过 5MB!')
        this.fileList = []
        return false
      }

      var filetype = file.type.toLowerCase()
      if (
        filetype.indexOf('jpg') == -1 &&
        filetype.indexOf('jpeg') == -1 &&
        filetype.indexOf('png') == -1
      ) {
        this.$message.error('图片类型错误')
        this.fileList = []
        return false
      }

      return new Promise(function(resolve, reject) {
        _this
          .getData({
            funcid: '1022',
            fileName: file.name
          })
          .then(res => {
            var data = res.data
            _this.fileInfo = data
            _this.uploadData = {
              key: data.dir + data.cloudname,
              policy: data.policy,
              OSSAccessKeyId: data.accessid,
              success_action_status: '200',
              signature: data.signature
            }
            resolve(_this.uploadData)
          })
      })
    },
    uploadSuccess(response, file, fileList) {
      this.form.roompicture =
        this.fileInfo.host + '/' + this.fileInfo.dir + this.fileInfo.cloudname
      this.fileList = []
    },
    uploadError(err, file, fileList) {
      this.fileList = []
    },

    refreshRoomData() {
      var _this = this
      _this
        .getData({ funcid: '1023', anchorid: _this.form.anchorid })
        .then(res => {
          if (res.code == 1) {
            _this.form = res.data
          }
        })
    },
    onSubmit() {
      var _this = this
      const para = {
        funcid: '1013',
        anchorid: _this.form.anchorid,
        roompicture: _this.form.roompicture,
        roomname: _this.form.roomname,
        anchorintro: _this.form.anchorintro
      }
      _this.editLoading = true
      _this.getData(para).then(res => {
        if (res.code == 1) {
          _this.$message({
            message: '保存成功!',
            type: 'success'
          })
        }
        _this.refreshRoomData()
        _this.editLoading = false
      })
    },
    push() {
      // this.isshow = false
    },
    handlePreview() {},
    handleRemove() {},
    beforeRemove() {},
    handleExceed() {}
  },
  activated() {
    // if (this.isshow) {
    //   this.isshow = true
    // }
  },
  computed: {
    headers() {
      return {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Request-Method': 'POST'
      }
    }
  },
  mounted() {
    var _this = this
    _this.$nextTick(() => {
      _this.$root.topbarbtntitle = ''
    })
    var room = sessionStorage.getItem('room')
    if (room) {
      _this.form = JSON.parse(room)
      _this.refreshRoomData()
    }
  }
}
</script>

<style scoped>
.room_setting {
  text-align: left;
}
.upload-demo {
  /* float: left; */
  /* top: 0;
  left: 100px; */
}
.head_icon {
  /* float: left; */
  width: 50px;
  height: 50px;
  border-radius: 25px;
}
.content {
  text-align: left;
}
.el-upload__tip {
  margin-left: 8px;
  display: inline-block;
}
.save_btn {
  float: left;
  margin-left: 140px;
}

/deep/ .el-input__inner {
  height: 30px;
  line-height: 30px;
  border-radius: 2px;
}
/deep/ .el-input__icon {
  line-height: 30px;
}
/deep/ .el-textarea__inner {
  border-radius: 2px;
}
</style>


