import axios from 'axios'
import qs from 'qs'
import { Message, MessageBox } from 'element-ui'
import {
  removeSession,
  getSession,
  getUserid,
  removeUserid,
  getAnchorid,
  removeAnchorid
} from './auth'

// 创建axios实例
const service = axios.create({
  // process.env.BASE_API
  baseURL: '', // api 的 base_url
  timeout: 50000 // 请求超时时间
})

// request拦截器
service.interceptors.request.use(
  config => {
    //session
    if (config.method === 'get') {
      var session = getSession()
      var userid = getUserid()
      // var anchorid = getAnchorid()
      if (session) {
        config.params.param.params.session = session
      }
      if (userid) {
        config.params.param.params.userid = userid
      }
      // if (anchorid) {
      //   config.params.param.params.anchorid = anchorid
      // }
    }
    // //解决axios post请求后台获取不到参数问题
    // if (config.method === 'post') {
    //   config.data = qs.stringify(config.data);
    // };
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// response 拦截器
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code == 1 || res.code == 200 || res.status == 200) {
      return response
    } else if (res.code == 2) {
      MessageBox.confirm(
        '你已被登出，可以取消继续留在该页面，或者重新登录',
        '确定登出',
        {
          confirmButtonText: '重新登录',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).then(() => {
        removeSession()
        removeUserid()
        //登录页面
        if (process.env.NODE_ENV === 'production') {
          //服务器对应地址
          location.replace('http://' + location.host + '/course/index/#/login')
        } else {
          location.replace('http://' + location.host + '/#/login')
        }
      })
      // return response
    } else {
      Message({
        message: res.info,
        type: 'error',
        duration: 5 * 1000
      })
      return response
    }
  },
  error => {
    console.log('err' + error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return error
  }
)

export default service



